
const state = {
    title: ''
}

const mutations = {
    set_title (state, title) {
        state.title = title
    }
}
const actions = {
    setTitle({commit},title) {
        commit('setTitle', title)
    }
}

const getters = {
    
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}