<template>
    <li :class="classes"> 
        <div class="media text-white" v-if="ignoredSupport">
            <img class="align-self-start mr-3" :src="picture" alt="Generic placeholder image" style="width:30px;height:30px;">
            <div class="media-body">
                <div class="d-flex justify-content-between">
                    <div>Intervento di {{ name }} Ignorato</div>
                    <span><date-time :at="datetime" /></span>
                </div>
            </div>
        </div>
        <div class="media" v-else-if="confirmedSupport">
            <img class="align-self-start mr-3" :src="picture" alt="Generic placeholder image" style="width:30px;height:30px;">
            <div class="media-body">
                <div class="d-flex justify-content-between">
                    <div><strong>Intervento di {{ name }} confermato</strong></div>
                    <span><date-time :at="datetime" /></span>
                </div>
            </div>
        </div>
        <div class="media" v-else>
            <img class="align-self-start mr-3" :src="picture" alt="Generic placeholder image">
            <div class="media-body">
                <div class="d-flex justify-content-between">
                    <div class="mb-3">
                        <router-link tag="h5" v-if="isContact" :to="`/contact/${cuid}`" class="m-0 font-weight-bold cursor-pointer">{{ name }}</router-link>
                        <h5 v-else class="m-0 font-weight-bold">{{ name }}</h5>
                        <span class="text-muted">{{ senderType === 'user' ? '' : email}}</span>
                    </div>
                    <span><date-time :at="datetime" /></span>
                </div>
                <div class="d-block mb-3"><p v-html="body"></p></div>
                <div class="d-block mb-3 font-italic" v-if="isUser">
                    <strong>{{ name }}</strong><br>
                    <div v-html="sign"></div>
                </div>
                <div class="d-block mb-3" v-if="attachments.length">
                    <ui-button tag="a" color="secondary" classes="mr-2" v-for="(file, index) in attachments" :key="index" rounded outline size="sm" icon="fas fa-paperclip" target="_blank" :href="attachmentLink(file)">{{ file }}</ui-button>
                </div>
                <div v-if="unconfirmedSupport">
                    <confirm-support :support="support" :reply_id="id" @sent="$emit('confirm')" :disabled="supportLoading" />
                    <ui-button @click="ignore" size="sm" color="secondary" icon="far fa-thumbs-down" :loading="supportLoading" :disabled="supportLoading">IGNORA INTERVENTO</ui-button>
                </div>
            </div>
        </div>
    </li>
</template>
<script>
    import services from '../../services'
    import ConfirmSupport from '../modals/ConfirmSupport'
    export default {
        name: 'request-answer',
        components: { ConfirmSupport },
        props: {
            id: {
                type: String | Number,
                required: false
            },
            type: {
                validator: value => {
                    return ['user', 'contact'].indexOf(value) > -1
                }
            },
            photo: {
                type: String,
                required: false,
                default: null
            },
            datetime: {
                type: String,
                required: true
            },
            name: {
                type: String,
                required: true
            },
            email: {
                type: String,
                required: true
            },
            body: {
                type: String,
                required: true
            },
            sign: {
                type: String,
                required: false,
                default: ''
            },
            attachments: {
                type: Array,
                required: false,
                default: () => {
                    return []
                }
            },
            senderType: {
                validator: value => {
                    return ['user', 'contact'].indexOf(value) > -1
                }
            },
            cuid: {
                type: Number,
                required: false
            },
            link: {
                type: Boolean,
                required: false,
                default: true
            },
            token: {
                type: String,
                required: true
            },
            support: {
                type: Object,
                required: false,
                default: null
            },
            own: {
                type: Boolean,
                required: false,
                default: false
            }
        },
        data: () => ({
            supportLoading: false
        }),
        computed: {
            classes () {
                return {
                    'rounded-0': true,
                    'list-group-item': true,
                    'p-4': !this.ignoredSupport && !this.confirmedSupport,
                    'bg-warning': this.confirmedSupport,
                    'bg-secondary': this.ignoredSupport
                }
            },
            unconfirmedSupport () {
                return this.own && this.support && this.support.done && this.support.confirmed == 0
            },
            confirmedSupport () {
                return this.own && this.support && this.support.done && this.support.confirmed == 1
            },
            ignoredSupport () {
                return this.own && this.support && this.support.done && this.support.confirmed == 2
            },
            isUser () {
                return this.senderType === 'user'
            },
            isContact () {
                return this.senderType === 'contact'
            },
            picture () {
                if (this.isUser) {
                    return services.userPhoto(this.photo)
                }
                return `${services.uri}/assets/images/no-photo.png`
            }
        },
        methods: {
            ignore () {
                this.supportLoading = true
                services.ignoreReplyFromSupport(this.id).then(response => {
                    this.$emit('ignore')
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.supportLoading = false
                })
            },
            attachmentLink (file) {
                return `${services.uri}/uploads/attachments/${this.token}/${file}`
            }
        }
    }
</script>
<style scoped>
    .cursor-pointer {
        cursor: pointer;
    }
    img {
        height:50px;
        width:50px
    }
</style>