<template>
    <div class="row">
        <analytics-item 
            title="Richieste"
            :now="requests.now"
            :comparison="requests.comparison"
            :loading="loading.requests" 
            v-model="day.requests" />

        <analytics-item 
            title="Contatti"
            :now="contacts.now"
            :comparison="contacts.comparison"
            :loading="loading.contacts" 
            v-model="day.contacts" />

        <analytics-item 
            title="Iscritti"
            :now="conversions.now"
            :comparison="conversions.comparison"
            :loading="loading.conversions"
            v-model="day.conversions" />

        <analytics-item 
            title="Ricavi"
            :now="earnings.now"
            :comparison="earnings.comparison"
            :loading="loading.earnings"
            v-model="day.earnings" />

    </div> 
</template>

<script>
    import services from '../../services'
    import AnalyticsItem from './AnalyticsItem';

    export default {
        components: {AnalyticsItem},
        data () {
            return  {
                loading: {
                    requests: true,
                    contacts: true,
                    conversions: true,
                    earnings: true
                },
                day: {
                    requests: 30,
                    contacts: 30,
                    earnings: 30,
                    conversions: 30
                },
                requests: {},
                contacts: {},
                earnings: {},
                conversions: {}
            }
        },
        mounted () {
            this.loadRequests()
            this.loadContacts()
            this.loadConversions()
            this.loadEarnings()
        },
        methods: {
            loadRequests() {
                this.loading.requests = true
                services.getAnalyticsWidgets('requests&day='+this.day.requests).then(data => {
                    this.requests = data
                }).catch(error => {
                    console.log(error)
                }).then(() => {
                    this.loading.requests = false
                })
            },
            loadContacts() {
                this.loading.contacts = true
                services.getAnalyticsWidgets('contacts&day='+this.day.contacts).then(data => {
                    this.contacts = data
                }).catch(error => {
                    console.log(error)
                }).then(() => {
                    this.loading.contacts = false
                })
            },
            loadConversions() {
                this.loading.conversions = true
                services.getAnalyticsWidgets('conversions&day='+this.day.conversions).then(data => {
                    this.conversions = data
                }).catch(error => {
                    console.log(error)
                }).then(() => {
                    this.loading.conversions = false
                })
            },
            loadEarnings() {
                this.loading.earnings = true
                services.getAnalyticsWidgets('earnings&day='+this.day.earnings).then(data => {
                    this.earnings = data
                }).catch(error => {
                    console.log(error)
                }).then(() => {
                    this.loading.earnings = false
                })
            }
        },
        watch: {
            'day.requests' (v) {
                this.loadRequests();
            },
            'day.contacts' (v) {
                this.loadContacts();
            },
            'day.conversions' (v) {
                this.loadConversions();
            },
            'day.earnings' (v) {
                this.loadEarnings();
            },
        }
    }
</script>

