<template>
    <div class="d-inline-block">
        <ui-button icon="fa fa-phone" color="success" @click="modal = true" classes="rounded-lg" size="sm">Telefonata in Entrata</ui-button>
        <modal :open="modal" @close="modal = false" size="lg" title="Inserisci Telefonata">
            <form ref="form">
                <div class="form-group row">
                    <label for="incoming_call_subject" class="col-sm-2 col-form-label text-right">Categoria</label>
                    <div class="col-sm-10">
                        <select v-model="category" name="subject" id="incoming_call_subject" class="custom-select rounded-0" required>
                            <option value="">Seleziona Categoria</option>
                            <option v-for="(category, index) in categories" :key="index" :value="index">{{category.name}}</option>
                        </select>
                    </div>
                </div>
                <div class="form-group row" v-if="courseSelectable">
                    <label for="incoming_call_subject" class="col-sm-2 col-form-label text-right">Corso</label>
                    <div class="col-sm-10">
                        <input type="text" v-model="subject" class="form-control" list="incoming_call_course_list" :required="courseSelectable">
                        <datalist id="incoming_call_course_list">
                            <option v-for="(course, index) in selectedCourses" :key="index">{{ course }}</option>
                        </datalist>
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_first_name" class="col-sm-2 col-form-label text-right">Nome *</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control" id="incoming_call_first_name" v-model="first_name">
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_last_name" class="col-sm-2 col-form-label text-right">Cognome *</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control" id="incoming_call_last_name" v-model="last_name">
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_email" class="col-sm-2 col-form-label text-right">Email *</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control" id="incoming_call_email" v-model="email">
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_telephone" class="col-sm-2 col-form-label text-right">Telefono</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control" id="incoming_call_telephone" v-model="telephone">
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_note" class="col-sm-2 col-form-label text-right">Note</label>
                    <div class="col-sm-10">
                        <editor :disabled="loading" id="note" tag="" v-model="note"></editor>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-2"></div>
                    <div class="col-sm-10">
                        <ul class="p-0">
                            <li class="text-danger" v-for="(error, index) in errors" :key="index">{{ error.join(',') }}</li>
                        </ul>        
                    </div>
                </div>
            </form>
            <template v-slot:footer>
                <ui-button icon="fa fa-times" color="outline-secondary" classes="btn-sm rounded-lg" @click="cancel">Annulla</ui-button>
                <ui-button icon="fa fa-phone" color="success" classes="btn-sm rounded-lg" :loading="loading" @click="submit">Conferma</ui-button>
            </template>
        </modal>
    </div>
</template>

<script>
    import services from '../../services'
    import { CATEGORY } from '../../constants'
    import LS from '../../services/localstorage'
    import Modal from '../layout/Modal'

    export default {
        components: { Modal },
        name: 'incoming-calls',
        props: {
            draft_id: {
                type: Number,
                required: false,
                default: null
            },
            default_first_name: {
                type: String,
                required: false,
                default: ''
            },
            default_last_name: {
                type: String,
                required: false,
                default: ''
            },
            default_email: {
                type: String,
                required: false,
                default: ''
            },
            default_telephone: {
                type: String,
                required: false,
                default: ''
            },
            default_note: {
                type: String,
                require: false,
                default: ''
            }
        },
        computed: {
            selectedCourses () {
                return this.courses[this.categories[this.category].slug]
            },
            courseSelectable () {
                return this.categories[this.category] ? this.categories[this.category].id > 1 : false
            },
            selectedCategory () {
                return this.categories[this.category] ? this.categories[this.category].id : null
            }
        },
        data () {
            return {
                errors: [],
                loading: false,
                modal: false,
                first_name: this.default_first_name,
                last_name: this.default_last_name,
                email: this.default_email,
                telephone: this.default_telephone,
                note: this.default_note,
                category: '',
                subject: '',
                categories: [
                    {
                        id: CATEGORY.GENERICA,
                        slug: 'generic',
                        name: 'Generica'
                    },
                    {
                        id: CATEGORY.CORSI_DI_LAUREA,
                        slug: 'degree',
                        name: 'Corsi di Laurea'
                    },
                    {
                        id: CATEGORY.MASTER_1_LIVELLO,
                        slug: 'm1l',
                        name: 'Master 1° Livello'
                    },
                    {
                        id: CATEGORY.MASTER_2_LIVELLO,
                        slug: 'm2l',
                        name: 'Master 2° Livello'
                    },
                    {
                        id: CATEGORY.ALTA_FORMAZIONE,
                        slug: 'alfo',
                        name: 'Alta Formazione'
                    },
                    {
                        id: CATEGORY.PERFEZIONAMENTI,
                        slug: 'perf',
                        name: 'Perfezionamenti'
                    },
                    {
                        id: CATEGORY.ESAMI_SINGOLI,
                        slug: 'esami-singoli',
                        name: 'Esami Singoli'
                    },
                    {
                        id: CATEGORY.CFU_24,
                        slug: '24-cfu',
                        name: '24 CFU'
                    }
                ],
                courses: []
            }
        },
        mounted () {
            services.getCourses().then(courses => {
                this.courses = courses;
            })
        },
        methods: {
            cancel () {
                this.reset()
                this.modal = false
            },
            submit () {
                if (this.category === '') {
                    return this.showErrors({category: ['Categoria non selezionata']})
                }
                this.loading = true
                services.sendCall({
                    user_id: LS.get('user').id,
                    direction: 1,
                    category: this.selectedCategory,
                    first_name: this.first_name,
                    last_name: this.last_name,
                    email: this.email,
                    telephone: this.telephone,
                    subject: this.subject,
                    note: this.note,
                    response: 3
                }).then(response => {
                    this.loading = false
                    if (response) {
                        this.reset()
                        this.modal = false
                    }
                }).catch(error => {
                    this.loading = false
                    this.showErrors(error)
                })
            },
            showErrors (error) {
                this.errors = error
                setTimeout(() => {
                    this.errors = []
                }, 4000)
            },
            reset () {
                this.errors = []
                this.first_name = ''
                this.last_name = ''
                this.email = ''
                this.telephone = ''
                this.note = ''
                this.category = ''
                this.note = ''
                this.subject = ''
            }
        }
    }
</script>