<template>
    <div>
        <button 
            @click="$router.go(-1)" 
            class="btn btn-outline-secondary btn-sm rounded-lg px-3"
            ><i class="fas fa-chevron-left mr-2"></i>Torna Indietro</button>
        <ui-button
            v-if="!assigned && status != 4" 
            @click="$emit('assign')"
            color="success"
            size="sm"
            icon="far fa-hand-point-right"
            :disabled="this.loading.assign"
            :loading="this.loading.assign"
            classes="rounded-lg"
        >Prendi in Carico</ui-button>
        <ui-button
            v-if="own && status == 3"
            @click="$emit('worked')"
            size="sm"
            color="success"
            icon="fas fa-lock"
            :loading="loading.worked"
            :disabled="loading.worked"
            classes="rounded-lg"
        >Segna come "Lavorata"</ui-button>
        <ui-button
            v-if="own && status !== 3"
            @click="$emit('work')"
            size="sm" icon="fas fa-lock-open" color="primary"
            :loading="loading.towork"
            :disabled="loading.towork"
            classes="rounded-lg"
        >Segna come "In Lavorazione"</ui-button>
        <send-support v-if="own && !support" :request_id="request_id" @sent="$emit('support')" />
        <ui-button
            v-if="own && status !== 6"
            @click="$emit('trash')"
            size="sm" icon="far fa-trash-alt" color="dark"
            :loading="loading.trash"
            :disabled="loading.trash"
            classes="rounded-lg"
        >Sposta in Cestino</ui-button>
    </div>
</template>
<script>

    import SendSupport from '../modals/SendSupport'

    export default {
        name: 'request-toolbar',
        components: { SendSupport },
        props: {
            loading: {
                type: Object,
                default: {
                    assign: false,
                    trash: false,
                    worked: false,
                    towork: false,
                    support: false
                },
                required: false
            },
            status: {
                type: Number,
                required: true
            },
            own: {
                type: Boolean,
                required: false,
                default: false
            },
            assigned: {
                type: Boolean,
                required: false,
                default: false
            },
            request_id: {
                type: String,
                required: true
            },
            support: {
                required: false,
                default: null
            }
        }
    }
</script>