<template>
    <div class="d-inline-block">
        <ui-button icon="fa fa-phone" color="danger" @click="modal = true" size="sm" classes="rounded-lg">Telefonata in Uscita</ui-button>
        <modal :open="modal" @close="modal = false" size="lg" title="Inserisci Telefonata in Uscita">
            <form ref="form">
                <div class="form-group row">
                    <label for="outgoing_call_response" class="col-sm-2 col-form-label text-right">Responso *</label>
                    <div class="col-sm-10">
                        <select v-model="response" required class="custom-select rounded-0">
                            <option value="0">Seleziona</option>
                            <option value="1">Nessuna Risposta</option>
                            <option value="2">Non Interessato</option>
                            <option value="3">Poco Interessato</option>
                            <option value="4">Molto Interessato</option>
                            <option value="5">Da Ignorare</option>
                        </select>
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_note" class="col-sm-2 col-form-label text-right">Note</label>
                    <div class="col-sm-10">
                        <editor :disabled="loading" id="note" tag="" v-model="note"></editor>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-2"></div>
                    <div class="col-sm-10">
                        <ul class="p-0">
                            <li class="text-danger" v-for="error in errors">{{ error.join(',') }}</li>
                        </ul>        
                    </div>
                </div>
            </form>
            <template v-slot:footer>
                <ui-button icon="fa fa-times" color="outline-secondary" classes="btn-sm rounded-lg" @click="cancel">Annulla</ui-button>
                <ui-button icon="fa fa-phone" color="success" classes="btn-sm rounded-lg" :loading="loading" @click="submit">Conferma</ui-button>
            </template>
        </modal>
    </div>
</template>
<script>
    import services from '../../services'
    import LS from '../../services/localstorage'
    import Modal from '../layout/Modal'

    export default {
        components: { Modal },
        name: 'outgoing-calls',
        props: {
            contact_id: {
                type: Number,
                required: true
            }
        },
        data () {
            return {
                errors: [],
                loading: false,
                modal: false,
                response: "",
                note: '',
                loading: false,
            }
        },
        methods: {
            cancel () {
                this.reset()
                this.modal = false
            },
            submit () {
                if (this.category === '') {
                    return this.showErrors({category: ['Categoria non selezionata']})
                }
                this.loading = true
                services.sendOutgoingCall({
                    user_id: LS.get('user').id,
                    contact_id: this.contact_id,
                    direction: this.direction,
                    note: this.note,
                    response: this.response,
                    direction: 2,
                }).then(response => {
                    this.loading = false
                    if (response) {
                        this.$emit('update')
                        this.reset()
                        this.modal = false
                    }
                }).catch(error => {
                    this.loading = false
                    this.showErrors(error)
                })
            },
            showErrors (error) {
                this.errors = error
                setTimeout(() => {
                    this.errors = []
                }, 4000)
            },
            reset () {
                this.errors = []
                this.response = ''
                this.note = ''
            }
        }
    }
</script>