<template>
    <div class="chat-container" v-if="online">
        <div class="conversation-container">
            <conversation 
                v-for="user in openChatUsers.reverse()"
                :minimized="user.minimized"
                :key="user.id"
                :user="user"
                :typing="user.typing"
                :messages="_messages(user.id)"
                :more_history="moreHistory(user.id)"
                :read="chatRead(user.id)"
                :messages_count="_unread(user.id)"
                :group="user.group"
                @toggle="toggleChat"
                @close="removeChat(user.id)"
            />
        </div>
        <div class="chat-users">
            <div class="d-flex">
                <div class="flex-grow-1 font-weight-bold text-uppercase">Chat ({{ onlineUsersCount }})</div>
                <div class="text-right">
                    <span :class="badgeClass" v-if="totalUnreadCount">{{ totalUnreadCount }}</span>
                </div>
            </div>
            
            <div class="position-relative">
                <i class="fas fa-search"></i>
                <input v-model="userFilter" type="text" placeholder="Cerca utente" class="form-control my-2" />
            </div> 

            <div class="chat-users-body">
                <div class="chat-users-list">
                    <chat-user
                        v-for="user in sortedUsers"
                        :id="user.id"
                        :group="user.group_name"
                        :read="chatRead(user.id)"
                        :online="user.online"
                        :photo="userPhoto(user.photo)"
                        :typing="user.typing"
                        :first_name="user.first_name"
                        :last_name="user.last_name"
                        :messages_count="_unread(user.id, user.group)"
                        :key="user.id"
                        @click.native="openChat(user.id)"
                    />
                </div>
            </div>
        </div>
    </div>
    <div v-else>Offline</div>
</template>
<script>
    import { mapState, mapGetters } from 'vuex'
    import Conversation from './chat/Conversation'
    import ChatUser from './chat/ChatUser'
    import services from '../services'
    import LS from '../services/localstorage'
    import store from '../store'
    import Vue from 'vue'

    export default {
        name: 'chat-component',
        components: { Conversation, ChatUser },
        data () {
            return {
                online: false,
                chats: [],
                animation: false,
                userFilter: ''
            }
        },
        iconTimeout: null,
        computed: {
            ...mapState('chat', {
                messages: 'messages',
            }),
            ...mapGetters('chat', {
                allUsers                 : 'allUsers',
                onlineUsers              : 'onlineUsers',
                offlineUsers             : 'offlineUsers',
                unreadMessages           : 'unreadMessages',
                unreadMessagesCount      : 'unreadMessagesCount',
                unreadGroupMessagesCount : 'unreadGroupMessagesCount',
                chatRead                 : 'chatRead'
            }),
            sortedUsers ()
            {
                let sorted = [...this.allUsers]
                sorted.sort((a, b) => {
                    if (a.group && !b.group) return -1
                    else if (!a.group && b.group) return 1
                    if (a.online && !b.online) return -1
                    if (!a.online && b.online) return 1
                    else {
                        const unread_a = this._unread(a.id)
                        const unread_b = this._unread(a.id)
                        if (unread_a > unread_b) return 1
                        else if (unread_a < unread_b) return -1
                        else {
                            if (a.last_name > b.last_name) return 1
                            if (a.last_name < b.last_name) return -1
                            return 0
                        }
                    } 
                })
                if (this.userFilter) {
                    sorted = sorted.filter(u => {
                        const firstlast = u.first_name + ' ' + u.last_name
                        const lastfirst = u.first_name + ' ' + u.last_name
                        const reg = new RegExp(`${this.userFilter}`, 'gi')
                        return reg.test(firstlast) || reg.test(lastfirst)
                    })
                }
                
                return sorted.filter(user => user.id != this.currentUserId)
            },
            totalUnreadCount () {
                return this.unreadMessagesCount.filter((index, c) => c !== undefined).reduce((total, current) => total + current, 0)
            },
            openChatUsers ()
            {
                return this.chatUsers.filter(u => {
                    return this.chats.find(c => c.id === u.id)
                }).map(u => {
                    const index = this.chats.findIndex(c => c.id === u.id)
                    return {
                        ...u,
                        minimized: this.chats[index].minimized
                    }
                })
            },
            chatUsers ()
            {
                return this.allUsers.filter(u => u.id !== LS.get('user').id)
            },
            currentUserId ()
            {
                return LS.get('user').id
            },
            onlineUsersCount ()
            {
                return this.chatUsers.filter(u => u.online).length
            },
            badgeClass ()
            {
                return {
                    badge: true,
                    'badge-danger': true,
                    animated: true,
                    flash: true
                }
            }
        },
        getChatStorage () {
            if (LS.get('chat')) {
                return LS.get('chat').find(user =>  user.id === parseInt(LS.get('user').id))
            } else {
                return null
            }
        },
        initChatStorage () {
            const uid = parseInt(LS.get('user').id)
            let chatStorage = LS.get('chat')
            if (!chatStorage) {
                chatStorage = []
                
            }
            chatStorage.push({
                id: uid,
                chats: []
            })
            LS.set('chat', chatStorage)
        },
        storeChatInStorage (id, minimized) {
            id = parseInt(id)
            const uid = parseInt(LS.get('user').id)
            const chatStorage = LS.get('chat')
            const userIndex = chatStorage.findIndex(user => user.id === uid)
            if (!chatStorage[userIndex].chats.find(chat => chat.id === id)) {
                chatStorage[userIndex].chats.push({
                    id,
                    minimized
                })
            }
            LS.set('chat', chatStorage)
        },
        deleteChatInStorage (id) {
            id = parseInt(id)
            const uid = parseInt(LS.get('user').id)
            const chatStorage = LS.get('chat')
            const userIndex = chatStorage.findIndex(user => user.id === uid)
            const chatIndex = chatStorage[userIndex].chats.findIndex(chat => chat.id === id)
            chatStorage[userIndex].chats.splice(chatIndex, 1)
            LS.set('chat', chatStorage)
        },
        updateChatStatusInStorage (id, status) {
            id = parseInt(id)
            const uid = parseInt(LS.get('user').id)
            const chatStorage = LS.get('chat')
            const userIndex = chatStorage.findIndex(user => user.id === uid)
            const chatIndex = chatStorage[userIndex].chats.findIndex(chat => chat.id === id)
            chatStorage[userIndex].chats[chatIndex].minimized = status
            LS.set('chat', chatStorage)
        },
        methods: {
            userPhoto (photo) {
                return services.userPhoto(photo)
            },
            toggleChat (id) {
                const index = this.chats.findIndex(c => c.id === id)
                const minimized = !this.chats[index].minimized
                Vue.set(this.chats, index, {
                    ...this.chats[index],
                    minimized
                })
                this.$options.updateChatStatusInStorage(id, minimized)
            },
            moreHistory (id) {
                return this.chats.find(c => c.id === id).more_history
            },
            _messages (id) {
                return this.messages.filter(m => m.sender == id || m.recipient == id)
            },
            _unread (id, group) {
                if (group) console.log(this.unreadGroupMessagesCount)
                return group ? this.unreadGroupMessagesCount[id] : this.unreadMessagesCount[id]
            },
            removeChat (id) {
                this.chats.splice(this.chats.findIndex(c => c.id === id), 1)
                this.$options.deleteChatInStorage(id)
            },
            openChat (id, minimized = false) {
                const index = this.chats.findIndex(c => c.id === id)
                if (index > -1) {
                    this.$options.updateChatStatusInStorage(id, minimized)
                    return Vue.set(this.chats, index, {
                        ...this.chats[index],
                        minimized: minimized
                    })
                }
                this.chats.push({
                    id,
                    more_history: true,
                    minimized: minimized
                })
                this.$options.storeChatInStorage(id, minimized)
            },
            playNotification () {
                services.playSound('chat.mp3')
            }
        },
        mounted () {
            
            services.chat.on('USERS', users => {
                store.commit('chat/set_users', users)
            }).on('USER_CONNECTED', user_id => {
                store.commit('chat/set_online_user', user_id)
            }).on('USER_DISCONNECTED', user_id => {
                store.commit('chat/set_offline_user', user_id)
            }).on('CHAT_MESSAGE', message => {
                if (message.sender != LS.get('user').id) {
                    /**
                     * TODO: if focused handle the situation like playsound and send automatically read
                    */
                    this.playNotification()
                    this.animation = true
                    clearTimeout(this.$options.iconTimeout)
                    this.$options.iconTimeout = setTimeout(() => this.animation = false, 600)
                    store.commit('chat/unset_user_typing', message.sender)
                }
                
                store.commit('chat/add_message', {...message, read_at: null})
            }).on('CHAT_READ', user_id => {
                store.commit('chat/set_sent_messages_as_read', {id: user_id, read_at: (new Date()).toISOString()})
            }).on('TYPING', user_id => {
                store.commit('chat/set_user_typing', user_id)
            }).on('HISTORY', ({ id, messages, last_id }) => {
                if (messages.length < 10) {
                    const index = this.chats.findIndex(c => c.id === id)
                    Vue.set(this.chats, index, {
                        ...this.chats[index],
                        more_history: false
                    })
                }
                store.commit('chat/history', messages)
            }).on('UNREAD_MESSAGES', messages => {
                store.commit('chat/history', messages)
            }).on('connect', () => {
                services.chat.send('USERS')
                services.chat.send('UNREAD_MESSAGES')
                this.online = true

                let chatStorage = this.$options.getChatStorage()
                if (!chatStorage) {
                    this.$options.initChatStorage()
                    chatStorage = this.$options.getChatStorage()
                }
                for (let i in chatStorage.chats) {
                    this.openChat(chatStorage.chats[i].id, chatStorage.chats[i].minimized)
                }
            }).on('close', () => {
                this.online = false
            })
        },
        beforeDestroy () {
            services.chat
                .off('USERS')
                .off('USER_CONNECTED')
                .off('USER_DISCONNECTED')
                .off('USER_MESSAGE')
                .off('HISTORY')
                .off('READ_MESSAGES')
                .off('UNREAD_MESSAGES')
                .off('connect')
                .off('close')
        }
    }
</script>
<style scoped>
    i.fa-search {
        position: absolute;
        top: 8px;
    }

    input {
        background-color: transparent;
        border-top: 0;
        border-right: 0;
        border-left: 0;
        border-radius: 0;
        text-indent:12px;
    }

    .form-control:focus {
        background-color: transparent!important;

    }

    .chat-users .chat-users-header {
        height: 35px;
        line-height: 15px;
        background: #333;
        color: white;
    }

    .chat-users .chat-users-list {
        overflow-y: auto;
        transition: all .3s ease;
    }

    .chat-users .chat-users-body {
        overflow-x: hidden;
        overflow-y: auto;
        max-height: 390px;
    }

    .chat-users.minimized .chat-users-list {
        transition: all .3s ease;
    }

    .conversation-container {
        position: fixed;
        bottom: 0;
        right: 0;
        display: flex;
        align-items: flex-end;
        padding: 0 20px;
        z-index: 10;
    }
</style>