<template>
    <div>
        <div v-if="open" class="backdrop" @click="open = false" />
        <transition enter-active-class="animated slideInUp faster" leave-active-class="animated slideOutDown faster">
            <div v-if="open" class="modal show" tabindex="-1" role="dialog" style="display: block; z-index: 1100">
                <div :class="dialogClasses" role="document">
                    <div class="modal-content">
                        <div class="modal-header">
                            <h5 class="modal-title">{{ title }}</h5>
                            <button type="button" class="close" data-dismiss="modal" aria-label="Close" @click="$emit('close')">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div class="modal-body">
                            <slot></slot>
                        </div>
                        <div class="modal-footer">
                            <slot name="footer">
                                <button type="button" class="btn btn-outline-secondary btn-sm px-3 rounded-lg" @click="$emit('close')">Chiudi</button>
                                <button type="button" class="btn btn-success btn-sm px-3 rounded-lg">Conferma</button>
                            </slot>
                        </div>
                    </div>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
    export default {
        props: {
            title: {
                type: String,
                required: false,
                default: 'Title'
            },
            open: {
                type: Boolean,
                required: false,
                default: false
            },
            size: {
                type: String,
                required: false,
                default: ''
            }
        },
        computed: {
            dialogClasses() {
                return {
                    'modal-dialog': true,
                    'modal-xl': this.size === 'xl',
                    'modal-lg': this.size === 'lg'
                }
            }
        }
    }
</script>

<style scoped lang="scss">
    p:hover ~ span {
        background: red;
    }
    
    .modal {
        display: block !important;
    }
    .backdrop {
        position: fixed;
        background: rgba(0, 0, 0, .4);
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 1099;
    }
</style>