<template>
    <li class="rounded-0 list-group-item p-4"> 
        <div class="media">
            <router-link :to="`/contact/${request.cid}`" class="request-contact p-0">
                <img class="align-self-start mr-3 rounded-circle" :src="picture" style="height:50px;width:50px">
            </router-link>
            <div class="media-body">
                <div class="d-flex justify-content-between mt-1 mb-4">
                    <div>
                        <h5 class="m-0 font-weight-bold">
                            <router-link v-if="link" class="p-0" :to="`/contact/${request.cid}`">{{ request.first_name + ' ' + request.last_name }}</router-link>
                            <span v-else class="p-0"> {{ request.first_name }} {{ request.last_name}}</span>
                        </h5>
                        <span class="text-muted">{{ request.email }}</span>
                    </div>
                    <date-time :at="request.created_at" />
                </div>
                <div class="d-block mb-3">
                    <div v-if="request.type === 1"> 
                        <div v-html="request.body"></div>
                    </div>
                    <div v-else-if="request.type === 2"> 
                        <request-body-try 
                            :first_name="request.first_name"
                            :last_name="request.last_name"
                            :fiscal_code="request.fiscal_code"
                            :email="request.email"
                            :course_name="request.subject"
                        />
                    </div>
                    <div v-else-if="request.type === 3">
                        <request-body-evaluation 
                            :first_name="request.first_name"
                            :last_name="request.last_name"
                            :fiscal_code="request.fiscal_code"
                            :birth_date="request.birth_date"
                            :telephone="request.telephone"
                            :email="request.email"
                            :course_name="request.subject"
                        />
                    </div>
                    <div v-else-if="request.type === 4">
                        <request-body-enrol 
                            :first_name="request.first_name"
                            :last_name="request.last_name"
                            :email="request.email"
                            :fiscal_code="request.fiscal_code" 
                            :telephone="request.telephone"
                            :course="request.subject"
                            :enrol="{
                                code: request.enrol_code,
                                type: request.enrol_type,
                                classform: request.enrol_classform,
                                ecp: request.enrol_ecp,
                                convention: request.enrol_convention,
                                program: request.enrol_program,
                                import: request.enrol_import
                            }"
                        />
                    </div>
                </div>
                
                <div class="d-block mb-3">
                    <ui-button 
                        tag="a" 
                        color="secondary" 
                        classes="mr-2 rounded-lg" 
                        v-for="(file, index) in request.attachments" 
                        :key="index" 
                        rounded 
                        outline 
                        size="sm" 
                        icon="fas fa-paperclip" 
                        :href="`https://crm.unimercatorum.it/uploads/attachments/${request.token}/${file}`" 
                        target="_blank">{{ file }}</ui-button>
                </div>
                <div class="d-block mb-3">
                    <button v-if="request.telephone" type="button" class="btn btn-sm btn-outline-success rounded-lg"><i class="fas fa-phone mr-2"></i>{{ request.telephone }}</button>
                    <button v-if="request.marketing" type="button" class="btn btn-sm btn-outline-success rounded-lg"><i class="fas fa-check mr-2"></i>Autorizza all'invio di materiale informativo</button>
                </div>
                <div class="d-block mb-3 request-student-check">
                    <button type="button" class="btn btn-sm btn-outline-success request-official-code d-none"><i class="fas fa-qrcode mr-2"></i><span></span></button>
                    <button type="button" class="btn btn-sm btn-outline-success request-course d-none"><i class="far fas fa-graduation-cap mr-2"></i><span></span></button>
                    <button type="button" class="btn btn-sm btn-outline-success request-student-at d-none"><i class="fas fa-check mr-2"></i><span></span></button>
                </div>
            </div>
        </div>
    </li>
</template> 

<script>
    import RequestBodyEnrol from './RequestBodyEnrol'
    import RequestBodyEvaluation from './RequestBodyEvaluation'
    import RequestBodyTry from './RequestBodyTry'
    import services from '../../services'

    export default {
        components: {
            RequestBodyEnrol,
            RequestBodyEvaluation,
            RequestBodyTry
        },
        props: {
            request: {
                type: Object,
                required: true
            },
            attachments: {
                type: Array,
                required: false
            },
            link: {
                type: Boolean,
                required: false,
                default: true
            }
        },
        computed: {
            picture () {
                return `${services.uri}/assets/images/no-photo.png`
            }
        }
    }
</script>
