<template>
    <header class="bg-white shadow-sm no-print">
        <div class="d-flex">
            <div>
                <div class="bg-logo h-100 d-flex align-items-center">
                    <router-link :to="homeLink" class="cursor-pointer h5 text-white mb-0 d-flex pl-4 align-items-center" tag="div">
                        <div><i class="far fa-compass mr-3 fa-2x"></i></div>
                        <div style="line-height: 15px;top: 3px;position: relative;">
                            <span><strong>CRM</strong> Multiversity</span> 
                            <span class="small">unimercatorum.it</span>
                        </div>
                    </router-link>
                </div>
            </div>
            <div class="px-5 w-100">
                <nav class="my-3">
                    <div class="d-flex align-items-center">

                        <div class="position-relative w-50" v-click-outside="closeSearchResults">
                            <input 
                                type="text" 
                                class="form-control rounded-lg bg-white border-2" 
                                placeholder="Cerca contatto" 
                                v-model="search" 
                                @focus="searchContact" 
                                @blur="focused = false" 
                                :readonly="!focused">
                            <div class="position-absolute w-100 bg-white shadow-lg border search" v-if="show">
                                <div v-if="loading" class="text-secondary text-center"><i class="fas fa-circle-notch fa-spin"></i></div>
                                <router-link tag="div" class="px-3 py-2 bg-white row no-gutters border-bottom" v-for="(contact, index) in this.search_results" :to="`/contact/${contact.id}`" :key="index">
                                    <div class="col-6 text-capitalize">
                                        {{ contact.first_name.toLowerCase() }} {{ contact.last_name.toLowerCase() }}
                                    </div>
                                    <div class="col-6 text-right">{{ contact.email }}</div>
                                </router-link>
                            </div>
                        </div>

                        <div class="w-50">
                            <div class="d-flex justify-content-end align-items-center">
                                <div class="mr-5">
                                    <a href="https://tower.multiversity.click/" target="_blank">
                                        <i class="far fa-address-book mr-2"></i>Rubrica
                                    </a>
                                </div>
                                <notifications />
                                <div class="dropdown" v-click-outside="closeDropDown">
                                    <div 
                                        @click="open = !open" 
                                        id="navbarDropdown" 
                                        role="button" 
                                        data-toggle="dropdown" 
                                        aria-haspopup="true" 
                                        aria-expanded="false"
                                        class="d-flex align-items-center"
                                        style="line-height:15px">
                                        <div class="mr-3">
                                            <img :src="'https://crm.unimercatorum.it/uploads/users/'+user.photo+''" width="35" class="rounded-circle">
                                        </div>
                                        <div> 
                                            <span class="d-block font-weight-bold">{{ user.first_name }} {{ user.last_name}}</span>
                                            <span class="text-muted small">{{ group }} </span>
                                        </div> 
                                    </div>
                                    <div class="dropdown-menu rounded-0" :class="{show: open}" aria-labelledby="navbarDropdown">
                                        <div class="dropdown-item color-secondary cursor-pointer" href="https://crm.unipegaso.app/logout" @click="logout"><i class="fas fa-sign-out-alt mr-2"></i>Esci</div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </nav>
            </div>
        </div>
    </header>
</template>

<script>
    import services from '../../services'
    import { mapState, mapActions, mapGetters } from 'vuex'
    import LS from '../../services/localstorage'
    import Notifications from './Notifications'

    export default {
        name: 'main-header',
        components: { Notifications },
        props: {
            menu: {
                type: Boolean,
                required: true
            }
        },
        data () {
            return {
                focused: false,
                open: false,
                search: '',
                search_results: [],
                loading: false,
                show: false,
            }
        },
        methods: {
            closeDropDown () {
                this.open = false
            },
            closeSearchResults () {
                this.search_results = []
                this.show = false
            },
            logout () {
                services.signout()
            },
            searchContact () {
                this.focused = true
                if (this.search.trim() === '') return
                clearTimeout(this.$options.searchTimeout)
                this.$options.searchTimeout = setTimeout(() => {
                    this.loading = true
                    this.show = true
                    this.search_results = []
                    services.searchContact(this.search.trim()).then(results => {
                        this.loading = false
                        this.search_results = results
                    })
                }, 500)
            }
        },
        computed: {
            ...mapState('auth', {
                user: 'user',
            }),
            ...mapGetters('auth', {
                group: 'group',
            }),
            homeLink () {
                const group = LS.get('user').group
                if (group === 1) {
                    return '/inbox'
                } else {
                    return '/support/inbox'
                }
            }
        },
        searchTimeout: null,
        watch: {
            search () {
                this.searchContact()
            }
        }
    }
</script>

<style scoped>
    .bg-logo {
        width:300px;
    }

    .dropdown-menu {
        top:30px!important;
    }

    .search {
        z-index:1;
        top:35px;
    }

    .search::before {
        border-bottom: 9px solid rgba(0, 0, 0, 0.2);
        border-left: 9px solid rgba(0, 0, 0, 0);
        border-right: 9px solid rgba(0, 0, 0, 0);
        content: "";
        display: inline-block;
        left: 10px;
        position: absolute;
        top: -9px;
    }
    .search::after {
        border-bottom: 8px solid #FFFFFF;
        border-left: 9px solid rgba(0, 0, 0, 0);
        border-right: 9px solid rgba(0, 0, 0, 0);
        content: "";
        display: inline-block;
        left: 10px;
        position: absolute;
        top: -8px;
    }

    .dropdown-menu-right::before {
        border-bottom: 9px solid rgba(0, 0, 0, 0.2);
        border-left: 9px solid rgba(0, 0, 0, 0);
        border-right: 9px solid rgba(0, 0, 0, 0);
        content: "";
        display: inline-block;
        right: 10px;
        position: absolute;
        top: -9px;
    }
    .dropdown-menu-right::after {
        border-bottom: 8px solid #FFFFFF;
        border-left: 9px solid rgba(0, 0, 0, 0);
        border-right: 9px solid rgba(0, 0, 0, 0);
        content: "";
        display: inline-block;
        right: 10px;
        position: absolute;
        top: -8px;
    }

</style>