<template>
    <div class="position-absolute p-5 d-flex flex-column position-0">

        <div><h4 class="font-weight-bold mb-4">{{ pageTitle }}</h4></div>

        <div class="row">
            <div class="col">
                <div class="bg-white rounded-top-lg shadow-sm">
                    <div class="row p-3">
                        <div class="col-6">
                            <button type="button" class="btn btn-outline-secondary btn-sm rounded-lg px-3" :class="{
                                disabled: loading
                            }" @click="refresh">
                                <i :class="{'fa-spin': loading}" class="fas fa-sync-alt mr-2 pause-spinner"></i>Aggiorna
                            </button>
                        </div>
                        <pager
                            :loading="loading"
                            :total="total"
                            :start="start"
                            :end="end"
                            :prev="prevDisabled"
                            :next="nextDisabled"
                            :pages="pages"
                            :page="page"
                            @prev="goPrev"
                            @next="goNext"
                            @change="p => go(p)"
                        />
                    </div>
                    <div class="bg-gray border-top border-bottom p-3 text-uppercase small">
                        <div class="row">
                            <div class="col-3">Nome</div>
                            <div class="col-4">Richiesta</div>
                            <div class="col-1">Da</div>
                            <div class="col-3">Operatore</div>
                            <div class="col-1">Data</div>
                        </div>
                    </div>                    
                </div>
            </div>
        </div>
        
        <div style="flex-grow: 1">
            <div class="position-relative slide-wrapper h-100 w-100" style="overflow-x: hidden">
                <horizontal-slide :dir="direction">
                    <ul class="list-group position-absolute h-100 w-100" :key="page">
                        <li v-if="loading" key="loading" class="rounded-0 list-group-item pointer bg-light text-center">
                            <span v-if="loading">
                                <i class="fas fa-circle-notch fa-spin"></i>
                            </span>
                        </li>
                        <li v-else-if="requests.length < 1" key="no-results" class="rounded-0 list-group-item pointer bg-light text-center">
                            <div>Nessuna richiesta presente</div>
                        </li>
                        <request-item v-for="(request, index) in requests" v-else-if="requests.length > 0" 
                            :support_pending="request.support_pending > 0"
                            :disabled="request.busy"
                            :key="index"
                            :id="request.id"
                            :contact="{
                                id: request.cid,
                                first_name: request.first_name,
                                last_name: request.last_name
                            }"
                            :user="{
                                id: request.user_id,
                                first_name: request.user_first_name,
                                last_name: request.user_last_name    
                            }"
                            :first_name="request.first_name"
                            :last_name="request.last_name"
                            :type="{
                                id: request.type,
                                class: request.type_class,
                                icon_color: request.type_color_icon,
                                name: request.type_name
                            }"
                            :subject="request.subject"
                            :status="{
                                id: request.status,
                                class: request.status_class,
                                icon: request.status_icon,
                                name: request.status_name
                            }"
                            :category="{
                                id: request.category,
                                name: request.category_name
                            }"
                            :replies_count="request.replies_count"
                            :updated_at="request.updated_at"
                            :provider="request.provider"
                        ></request-item>
                    </ul>
                </horizontal-slide>
            </div>
        </div>
    </div>
</template>

<script>
    import RequestItem from './inbox/RequestItem'
    import services from '../services'
    import Toolbar from './Toolbar'
    import HorizontalSlide from './transitions/HorizontalSlide'
    import AnalyticsWidgets from './analytics/AnalyticsWidgets'
    import { mapGetters, mapActions, mapState } from 'vuex'
    
    export default {
        name: 'inbox',
        components: {RequestItem, AnalyticsWidgets, HorizontalSlide, Toolbar},
        props: {
            type: {
                validator: function (value) {
                    return ['group', 'user'].indexOf(value) > -1
                }
            },
            page: {
                type: Number | String,
                required: false,
                default: 1
            },
            group: {
                type: Number | String,
                required: true
            },
            done: {
                type: Number | String,
                required: true
            },
            status: {
                type: String,
                required: true
            },
            own: {
                type: Number | String,
                required: false,
                default: null
            }
        },
        data () {
            return {
                start: 0,
                end: 0,
                quantity: 0,
                current_page: 1,
                pages: 0,
                total: 0,
                loading: false,
                requests: [],
                loading: false,
                direction: 1,
            }
        },
        computed: {
            prevDisabled () {
                return !this.prev || this.loading
            },
            nextDisabled () {
                return !this.next || this.loading
            },
            prevPageLink () {
                if (!this.prev) return this.$route.path
                return this.basePath + '/' + this.prev
            },
            nextPageLink () {
                if (!this.next) return this.$route.path
                return this.basePath + '/' + this.next
            },
            basePath () {
                if (this.$route.params && this.$route.params.page) {
                    const index = this.$route.path.lastIndexOf('/')
                    return this.$route.path.substring(0, index)
                }
                return this.$route.path
            },
            next () {
                const next = parseInt(this.page) + 1
                if (next > this.pages) return undefined
                return next
            },
            prev () {
                const prev = parseInt(this.page) - 1
                if (prev === 0) return undefined
                return prev
            },
            params () {
                return {
                    group: this.group,
                    status: this.status,
                    done: this.done,
                    own: this.own
                }
            },
            inInbox () {
                return /\/inbox(\/\d+)?/i.test(this.$route.path)
            },
            ...mapState('page', {
                pageTitle: 'title'
            })
        },
        methods: {
            goNext() {
                this.$router.push({params: {page: parseInt(this.page) + 1}})
            },
            goPrev() {
                this.$router.push({params: {page: parseInt(this.page) - 1}})
            },
            go (p) {
                this.$router.push({params: {page: parseInt(p)}})
            },
            refresh () {
                if (this.loading) return
                this.load()
            },
            load () {
                this.loading = true
                services.getSupportRequests(this.params).then(response => {
                    this.end = response.end
                    this.requests = response.items
                    this.page = response.page
                    this.pages = response.pages
                    this.quantity = response.qty
                    this.start = response.start
                    this.total = response.total
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading = false
                })
            }
        },
        mounted () {
            this.load()
        },
        watch: {
            params (new_value) {
                console.log(new_value)
                this.load()
            },
            page (new_value, prev_value) {
                const diff = prev_value - new_value
                this.direction = diff < 0 ? 1 : 0
                this.load()
            }
        }
        
    }
</script>
<style scoped>
    * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
    .left-0 {
        left: 0;
    }
    .right-0 {
        right: 0;
    }
</style>