<template>
    <div v-if="total > 0" class="col-6 text-right">
        <div class="btn-group pull-right">
            <div class="mt-1 mr-2">
                <span v-if="loading"><i class="fas fa-circle-notch fa-spin"></i></span>
                <span v-else><span class="range-start">{{ start }}</span>-<span class="range-end">{{ end }}</span></span>
                di <span class="range-total">{{ total }}</span>
            </div>
            <button :disabled="prev" @click="$emit('prev')" class="btn btn-outline-secondary btn-sm px-3 rounded-left prev"><i class="fa fa-chevron-left"></i></button>
            <select v-model="curpage" @change="change" class="custom-select custom-select-sm rounded-0 border-secondary border-left-0 border-right-0 border-top-1 border-bottom-1" style="width: auto">
                <option v-for="i in pages" :key="i" :value="i">{{ i }}</option>
            </select>
            <button :disabled="next" @click="$emit('next')" class="btn btn-outline-secondary btn-sm px-3 rounded-right next"><i class="fa fa-chevron-right"></i></button>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'inbox',
        data() {
            return {
                curpage: null
            }
        },
        mounted () {
            this.curpage = this.page
        },
        methods: {
            change (e) {
                this.$emit('change', e.target.value)
            }
        },
        watch: {
            page (n) {
                this.curpage = n
            }
        },
        props: {
            page: {
                type: Number | String,
                required: true
            },
            pages: {
                type: Number | String,
                required: true
            },
            start: {
                type: Number,
                required: true
            },
            end: {
                type: Number,
                required: true
            },
            total: {
                type: Number,
                required: true
            },
            prev: {
                type: Boolean,
                required: true
            },
            next: {
                type: Boolean,
                required: true
            },
            loading: {
                type: Boolean,
                required: true
            }
        }
    }
</script>