<template>
    <div class="position-absolute p-5 d-flex flex-column position-0"> 
        <div v-if="!contact" class="text-center py-4">
            <span style="font-size:25px"><i class="fas fa-circle-notch fa-spin"></i></span>
        </div>
        <div v-else>

            <div class="d-flex">
                <div><h4 class="font-weight-bolder mb-4">{{ name }}</h4></div> 
                <div class="ml-auto">
                    <incoming-calls />
                    <outgoing-calls @update="load()" v-if="contact" :contact_id="contact.id" />
                </div>
            </div>

            <div class="bg-white rounded-lg mb-3 shadow-sm">
                <div class="py-3 mx-4 border-bottom">
                    <ui-button
                        @click="$router.go(-1)" 
                        color="outline-secondary"
                        size="sm"
                        icon="fas fa-chevron-left"
                        classes="rounded-lg">
                        Torna Indietro
                    </ui-button>
                    <ui-button color="outline-secondary" size="sm" icon="far fa-edit">Modifica</ui-button>
                </div>
                <div class="py-3 mx-4 border-bottom">
                    <div class="row">
                        <div class="col-3">
                            <div class="d-block text-muted">Contatto</div> 
                        </div> 
                        <div class="col-9">
                            <div class="ml-4 font-weight-bold">{{ name }}</div>
                        </div>
                    </div>
                </div>
                <div class="py-3 mx-4 border-bottom">
                    <div class="row">
                        <div class="col-3">
                            <div class="d-block text-muted">E-Mail</div> 
                        </div> 
                        <div class="col-9">
                            <div class="ml-4 font-weight-bold">{{ contact.email }}</div>
                        </div>
                    </div>
                </div>
                <div class="py-3 mx-4 border-bottom">
                    <div class="row">
                        <div class="col-3">
                            <div class="d-block text-muted">Telefono</div> 
                        </div> 
                        <div class="col-9">
                            <div class="ml-4 font-weight-bold">{{ contact.telephone }}</div>
                        </div>
                    </div>
                </div>
                <div class="py-3 mx-4">
                    <div class="row">
                        <div class="col-3">
                            <div class="d-block text-muted">Codice Fiscale</div> 
                        </div> 
                        <div class="col-9">
                            <div class="ml-4 font-weight-bold text-uppercase">{{ contact.fiscal_code }}</div>
                        </div>
                    </div>
                </div>
            </div>

            <!-- Iscrizioni -->        
            <div><h4 class="font-weight-bolder my-4">Iscrizioni</h4></div>
            <div class="bg-white rounded-lg shadow-sm mb-3">
                <div class="p-3 text-center">In costruzione...</div>
            </div>

            <!-- Richieste -->
            <div><h4 class="font-weight-bolder my-4">Richieste</h4></div> 
            <div class="bg-white rounded-lg shadow-sm mb-3">
                <div class="border-bottom p-3">
                    <div class="row">
                        <div class="col-7">Richiesta</div>
                        <div class="col-1">Da</div>
                        <div class="col-3">Operatore</div>
                        <div class="col-1 text-right">Data</div>
                    </div>
                </div>
                <ul class="list-group" v-if="requests.length > 0">
                    <request-item v-for="(request, index) in requests"
                        :key="index"
                        :showName="false"
                        :id="request.id"
                        :contact="{
                            id: request.cid,
                            first_name: request.first_name,
                            last_name: request.last_name
                        }"
                        :user="{
                            id: request.user_id,
                            first_name: request.user_first_name,
                            last_name: request.user_last_name    
                        }"
                        :first_name="request.first_name"
                        :last_name="request.last_name"
                        :type="{
                            id: request.type,
                            class: request.type_class,
                            icon_color: request.type_color_icon,
                            name: request.type_name
                        }"
                        :subject="request.subject"
                        :status="{
                            id: request.status,
                            class: request.status_class,
                            icon: request.status_icon,
                            name: request.status_name
                        }"
                        :category="{
                            id: request.category,
                            name: request.category_name
                        }"
                        :replies_count="request.replies_count"
                        :updated_at="request.updated_at"
                        :provider="request.provider"
                    />
                </ul>
                <div v-else class="p-3 text-center">Nessuna richiesta</div>
            </div>

            <!-- Telefonate Call Center -->        
            <div><h4 class="font-weight-bolder my-4">Telefonate Call Center</h4></div>
            <div class="bg-white rounded-lg shadow-sm mb-3">
                <div class="p-3 text-center">In costruzione...</div>
            </div>

            <!-- Telefonate Orientamento -->
            <div><h4 class="font-weight-bolder my-4">Telefonate Orientamento</h4></div>
            <div class="bg-white rounded-lg shadow-sm mb-3">
                <div class="border-bottom p-3">
                    <div class="row">
                        <div class="col-7">Richiesta</div>
                        <div class="col-1">Note</div>
                        <div class="col-3">Operatore</div>
                        <div class="col-1 text-right">Data</div>
                    </div>
                </div>
                <ul class="list-group" v-if="calls.length > 0">
                    <call-item
                        :showName="false"
                        @note="note => modal = note"
                        v-for="(call, index) in calls"
                        :key="index"
                        :call="call"
                    />
                </ul>
                <div v-else class="p-3 text-center">Nessuna telefonata</div>
                <modal :open="modal ? true : false" @close="modal = false" title="Note Telefonata">
                    <template v-slot:footer>
                        <button data-v-3dea25d8="" type="button" data-dismiss="modal" class="btn btn-secondary" @click="modal = false">Chiudi</button>
                    </template>
                    {{ modal }}
                </modal>
            </div>

            <!-- Incontri in Sede -->
            <div><h4 class="font-weight-bolder my-4">Incontri in Sede</h4></div>
            <div class="bg-white rounded-lg shadow-sm mb-3">
                <div class="border-bottom p-3">
                    <div class="row">
                        <div class="col-7">Richiesta</div>
                        <div class="col-1">Note</div>
                        <div class="col-3">Operatore</div>
                        <div class="col-1 text-right">Data</div>
                    </div>
                </div>
                <ul class="list-group" v-if="meetings.length > 0">
                    <meeting-item
                        :showName="false"
                        @note="note => modal = note"
                        v-for="(meeting, index) in meetings"
                        :key="index"
                        :meeting="meeting"
                    />
                </ul>
                <div v-else class="p-3 text-center">Nessun incontro</div>
                <modal :open="modal ? true : false" @close="modal = false" title="Note Incontro">
                    <template v-slot:footer>
                        <button 
                            type="button"
                            class="btn btn-secondary" 
                            @click="modal = false">
                            Chiudi
                        </button>
                    </template>
                    {{ modal }}
                </modal>
            </div>

        </div>
    </div>
</template>

<script>
    import services from '../services'
    import RequestItem from './inbox/RequestItem'
    import CallItem from './call/CallItem'
    import MeetingItem from './meeting/MeetingItem'
    import Modal from './layout/Modal'
    import IncomingCalls from './modals/IncomingCalls'
    import OutgoingCalls from './modals/OutgoingCalls'

    export default {
        components: { 
            RequestItem, CallItem, MeetingItem, Modal, OutgoingCalls, IncomingCalls 
        },
        props: {
            id: {
                type: String,
                required: true
            }
        },
        data () {
            return {
                modal: false,
                loading: false,
                contact: false,
                requests: [],
                calls: [],
                meetings: []
            }
        },
        mounted () {
            this.load()  
        },
        methods: {
            load () {
                this.loading = true
                services.getContact(this.id).then(data => {
                    this.contact = data
                    this.requests = data.requests
                    this.calls = data.calls
                    this.meetings = data.meetings
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading = false
                })
            }
        },
        computed: {
            name () {
                return `${ this.contact.first_name } ${ this.contact.last_name}`
            }
        }
    }
</script>

<style scoped>
    ul li {
        list-style: none;
    }
</style>