<template>
    <div class="dropdown mr-5" v-click-outside="() => open = false">
        <div @click="open = !open">
            <span class="cursor-pointer"><i class="far fa-bell mr-2"></i>Notifiche</span>
            <span class="badge badge-danger position-relative" style="top:-2px" v-if="notification_count > 0">{{ notification_count > 10 ? '10+' : notification_count }}</span>
        </div>
        <div class="dropdown-menu dropdown-menu-right shadow-lg py-0" :class="{show: open}">
            <div class="max-height">
                <div :class="notificationClass(notification)" v-for="(notification, index) in notifications" :key="index"> 
                    <router-link 
                        tag="div" 
                        :to="'/' + notification.link" 
                        class="d-flex align-items-center">
                        <div class="mr-2">
                            <img :src="'https://crm.unimercatorum.it/uploads/users/' + notification.photo" width="40" class="rounded-circle">
                        </div>
                        <div class="d-flex flex-column">
                            <div v-html="notification.text"></div>
                            <date-time tag="small" classes="text-muted" :at="notification.created_at" />
                        </div>
                    </router-link>
                </div>
                <div v-if="last_id" @click="load" class="text-center dropdown-item border-bottom py-2 px-3 cursor-pointer">
                    {{ loading ? 'caricamento' : 'carica altre'}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState, mapActions } from 'vuex'
    
    export default {
        name: 'notifications',
        data () {
            return {
                open: false
            }
        },
        mounted () {
            this.load()
        },
        computed: {
            ...mapState('notification', {
                notifications: 'lasts',
                notification_count: 'count',
                last_id: 'last_id',
                loading: 'loading'
            })
        },
        methods: {
           ...mapActions('notification', {
                getNotifications: 'get',
                readNotifications: 'read',
                clickNotification: 'click'
            }),
            notificationClass (notification) {
                return {
                    'dropdown-item': true,
                    'border-bottom': true,
                    'py-2': true,
                    'px-3': true,
                    'cursor-pointer' : true,
                    'unclicked': !notification.clic_it
                }
            },
            load () {
                if (!this.loading) {
                    this.getNotifications()
                }
            }
        },
        watch: {
            open (v) {
                if (v) {
                    this.readNotifications()
                }
            }
        }
    }
</script>
<style scoped>
    .unclicked {
        background-color: #EDF2FA
    }
    .dropdown-menu-right::before {
        border-bottom: 9px solid rgba(0, 0, 0, 0.2);
        border-left: 9px solid rgba(0, 0, 0, 0);
        border-right: 9px solid rgba(0, 0, 0, 0);
        content: "";
        display: inline-block;
        right: 10px;
        position: absolute;
        top: -9px;
    }
    .dropdown-menu-right::after {
        border-bottom: 8px solid #FFFFFF;
        border-left: 9px solid rgba(0, 0, 0, 0);
        border-right: 9px solid rgba(0, 0, 0, 0);
        content: "";
        display: inline-block;
        right: 10px;
        position: absolute;
        top: -8px;
    }
    .dropdown-menu {
        top:30px!important;
    }
    .max-height{
        max-height: 385px;
        overflow-y: scroll;
    }
</style>