const LS = {
    get: key => {
        let data = localStorage.getItem(key)
        try {
            data = JSON.parse(data)
            return data
        } catch (e) {
            return localStorage.getItem(key)
        }
    },
    set: (key, value) => {
        if (typeof value === 'object') {
            value = JSON.stringify(value)
        }
        localStorage.setItem(key, value)
    },
    remove: key => {
        localStorage.removeItem(key)
    }
}

export default LS;