<template>
    <router-link tag="li" :to="`/request/${id}`" :class="liClass">
        <div class="row align-items-center">
            <div class="col-3" v-if="showName">
                <div class="d-block lh-100">
                    <span class="d-block text-capitalize" v-html="contactName"></span>
                </div>
            </div>
            <div :class="{
                'col-4': showName,
                'col-7': !showName,
                'text-truncate': true
            }">
                <span class="btn btn-xs mr-1" :class="type.class">{{ type.name }}</span>
                <span class="btn btn-xs btn-outline-secondary mr-1">{{ category.name }}</span>
                <span class="text-muted">{{ subject }}</span>
            </div>
            <div class="col-1"> 
                <span :class="providerClass" v-html="formatProvider"></span>
            </div>
            <div class="col-3 text-left">
                <div class="d-flex align-items-center">
                    <div class="w-50 pr-2 small">
                        <span v-if="user.id"><i class="far fa-user-circle mr-2"></i>{{ userName }}</span>
                    </div>
                    <div class="w-50">
                        <button @click.stop="assign" :class="`btn btn-sm ${support_pending ? 'btn-warning' : status.class} btn-block`" :disabled="loading">
                            <i v-if="loading" class="fas fa-circle-notch fa-spin"></i>
                            <i v-else :class="`${status.icon} mr-2`"></i>
                            {{ status.name }}
                        </button>
                    </div>
                </div>
            </div>
            <div class="col-1 text-right text-muted small"><date-time :at="updated_at" /></div>
        </div>
    </router-link>
</template>
<script>
    import services from '../../services'
    import { PROVIDER } from '../../constants'

    export default {
        props: {
            showName: {
                type: Boolean,
                required: false,
                default: true
            },
            id: {
                type: Number,
                required: false
            },
            user: {
                type: Object,
                required: false
            },
            contact: {
                type: Object,
                required: true
            },
            type: {
                type: Object,
                required: true
            },
            category: {
                type: Object,
                required: true
            },
            status: {
                type: Object,
                required: true
            },
            updated_at: {
                type: String,
                required: true
            },
            subject: {
                type: String,
                required: true
            },
            provider: {
                type: String,
                required: false,
                default: 'Sito Web'
            },
            replies_count: {
                type: Number,
                required: true
            },
            disabled: {
                type: Boolean,
                default: false,
                required: false
            },
            support_pending: {
                type: Boolean,
                default: false,
                required: false
            }
        },
        data () {
            return {
                loading: false,
                updated: false
            }
        },
        computed: {
            liClass () {
                return {
                    'shake animated': this.updated,
                    'rounded-0': true,
                    'pointer': true,
                    'bg-white': true,
                    'cursor-pointer': true,
                    'border-bottom': true,
                    'px-3': true,
                    'py-2': true,
                    disabled: this.disabled
                }
            },
            providerClass () {
                return {
                    small:  true,
                    'text-truncated': true,
                    'text-success': this.provider === PROVIDER.LANDING
                }
            },
            formatProvider() {
                switch (this.provider) {
                    case  PROVIDER.WEBSITE:
                        return '<i class="fas fa-desktop mr-1"></i>Sito Web'
                        break
                    case  PROVIDER.LANDING:
                        return '<i class="far fa-paper-plane mr-1"></i>Pubblicità'
                        break
                    case PROVIDER.CHATBOT: 
                        return '<i class="fab fa-facebook-messenger mr-1"></i>Messenger'
                        break
                    default:
                        return this.provider;
                }
            },
            contactName() {
                let contact_user = this.contact.first_name.toLowerCase()+ ' '+this.contact.last_name.toLowerCase()
                if (this.replies_count == 0) {
                    return contact_user
                } else {
                    return contact_user + ' <span class="text-muted small">'+(parseInt(this.replies_count)+1)+'</span>';
                }
            },
            userName() {
                return this.user.first_name.substring(0, 1) + '. ' + this.user.last_name
            }
        },
        methods: {
            assign () {
                if (this.status.id === 1 && !this.user.id) {
                    this.loading = true
                    services.assignRequest(this.id).then(data => {
                        if (data === true) {
                            this.$router.push(`/request/${this.id}`)
                        }
                    }).catch(error => {
                        alert(error)
                    }).then(() => {
                        this.loading = false
                    })
                } else {
                    this.$router.push(`/request/${this.id}`)
                }
            }
        },
        watch: {
            status (o, n) {
                if (o.id != n.id) {
                    this.updated = true
                    setTimeout(() => {
                        this.updated = false
                    }, 1000)
                }
                
            }
        }
    }
</script>
<style scoped>
    li.disabled {
        opacity: .5;
        transition: all .3s ease;
    }
    .list-group .btn-xs, table .btn-xs {
        padding: 0 .5rem;
        font-size: .875rem;
        line-height: 1.5;
        border-radius: .2rem;
    }
    .list-group-item {
        cursor: pointer;
    }
    * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
</style>