<template>
    <div class="col">
            <div class="bg-white p-3 shadow-sm rounded-lg">
                <div class="d-flex align-items-center mb-2">
                    <div class="font-weight-bold text-secondary">{{ title }}</div>
                    <div class="ml-auto">
                        <select 
                            class="custom-select custom-select-sm rounded-lg"
                            v-model="day">
                            <option value="30">30 Giorni</option>
                            <option value="90">90 Giorni</option>
                            <option value="365">1 anno</option>
                        </select>
                    </div>
                </div>
                <i class="fas fa-circle-notch fa-spin fa-3x text-muted" v-if="loading"></i>
                <h1 class="m-0"><span v-if="!loading">{{ now }}</span></h1>
                <div class="mt-2 font-weight-bold text-secondary">
                    <i class="fas fa-circle-notch fa-spin text-muted" v-if="loading"></i>
                    <span v-if="!loading"><span v-html="icon"></span>{{ comparison }}%</span>
                </div>
            </div>
        </div>
</template>

<script>
    export default {
        props: ['title', 'now', 'comparison', 'loading'],
        data() {
            return {
                day: 30
            }
        },
        computed: {
            icon() {
                if (parseFloat(this.comparison) < 0) {
                    return '<i class="fas fa-arrow-alt-circle-down mr-2 text-danger"></i>'
                } else {
                    return '<i class="fas fa-arrow-alt-circle-up mr-2 text-success"></i>'
                }
            }
        },
        watch: {
            day(v) {
                this.$emit('input', v)
            }
        }        
    }
</script>