<template>
    <div class="d-inline-block">
        <ui-button icon="fa fa-phone" color="success" @click="modal = true" classes="rounded-lg" size="sm">Telefonata in Entrata</ui-button>
        <modal :open="modal" @close="modal = false" size="lg" title="Inserisci Telefonata">
            <form ref="form">

                <div class="form-group row">
                    <label for="incoming_call_area" class="col-sm-2 col-form-label text-right">Area</label>
                    <div class="col-sm-10">
                        <v-select
                            v-model="area"
                            :options="areas"
                            :reduce="area => area.id"
                            :clearable="false"
                            label="name"
                            inputId="incoming_call_area"
                            placeholder="Seleziona Area"
                            required
                         />
                    </div>
                </div>

                <div class="form-group row" v-if="orientatoreSelectable">
                    <label for="incoming_call_to_user" class="col-sm-2 col-form-label text-right">Orientatore</label>
                    <div class="col-sm-10">
                        <v-select
                            v-model="to_user"
                            :options="[{first_name: 'Nessuno', last_name: '', id: 0}, ...users]"
                            :getOptionLabel="user => `${user.first_name} ${user.last_name}`"
                            :required="orientatoreSelectable"
                            :reduce="user => user.id"
                            :clearable="false"
                            inputId="incoming_call_to_user"
                            placeholder="Seleziona Orientatore"
                        />
                    </div>
                </div>

                <div class="form-group row" v-if="categorySelectable">
                    <label for="incoming_call_subject" class="col-sm-2 col-form-label text-right">Categoria</label>
                    <div class="col-sm-10">
                        <v-select
                            :options="categories"
                            :required="categorySelectable"
                            :reduce="category => category.id"
                            :resetOnOptionsChange="true"
                            :clearable="false"
                            v-model="category"
                            inputId="subject"
                            label="name"
                            placeholder="Seleziona Categoria"
                        />
                    </div>
                </div>

                <div class="form-group row" v-if="ecpSelectable">
                    <label for="ecp" class="col-sm-2 col-form-label text-right">ECP</label>
                    <div class="col-sm-10">
                        <v-select
                            :options="ecps"
                            :reduce="ecp => ecp.id"
                            :required="ecpSelectable"
                            :resetOnOptionsChange="true"
                            :clearable="false"
                            v-model="ecp"
                            inputId="ecp"
                            label="nome"
                            placeholder="Seleziona ECP/EiPoint"
                        />
                    </div>
                </div>

                <div class="form-group row" v-if="courseSelectable">
                    <label for="incoming_call_course_list" class="col-sm-2 col-form-label text-right">Corso</label>
                    <div class="col-sm-10">
                        <v-select
                            :options="selectedCourses"
                            :required="courseSelectable"
                            :resetOnOptionsChange="true"
                            :clearable="false"
                            v-model="subject"
                            label="course"
                            placeholder="Seleziona il Corso"
                        />
                    </div>
                </div>

                <div class="form-group row">
                    <label for="incoming_call_first_name" class="col-sm-2 col-form-label text-right">Nome</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control" id="incoming_call_first_name" v-model="first_name" required>
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_last_name" class="col-sm-2 col-form-label text-right">Cognome</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control" id="incoming_call_last_name" v-model="last_name" required>
                    </div>
                </div>
                <div class="form-group row" v-if="emailRequired">
                    <label for="incoming_call_email" class="col-sm-2 col-form-label text-right">Email</label>
                    <div class="col-sm-10">
                        <input type="email" class="form-control" id="incoming_call_email" v-model="email" required>
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_telephone" class="col-sm-2 col-form-label text-right">Telefono</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control" id="incoming_call_telephone" v-model="telephone">
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_note" class="col-sm-2 col-form-label text-right">Note</label>
                    <div class="col-sm-10">
                        <editor :disabled="loading" id="note" tag="" v-model="note"></editor>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-2"></div>
                    <div class="col-sm-10">
                        <ul class="p-0">
                            <li class="text-danger" v-for="(error, index) in errors" :key="index">{{ error.join(',') }}</li>
                        </ul>        
                    </div>
                </div>
            </form>
            <template v-slot:footer>
                <ui-button icon="fa fa-times" color="outline-secondary" classes="btn-sm rounded-lg" @click="cancel">Annulla</ui-button>
                <ui-button icon="fa fa-phone" color="success" classes="btn-sm rounded-lg" :loading="loading" @click="submit">Conferma</ui-button>
            </template>
        </modal>
    </div>
</template>
<script>
    import services from '../../services'
    import { AREA, CATEGORY } from '../../constants'
    import LS from '../../services/localstorage'
    import Modal from '../layout/Modal'

    export default {
        components: { Modal },
        name: 'incoming-callcenter-calls',
        computed: {
            areaSelectable () {
                return !!this.default_area
            },
            selectedCourses () {
                const slug = this.categories.find(category => category.id === this.category).slug
                return this.courses[slug]
            },
            courseSelectable () {
                return [
                    CATEGORY.CORSI_DI_LAUREA,
                    CATEGORY.MASTER_1_LIVELLO,
                    CATEGORY.MASTER_2_LIVELLO,
                    CATEGORY.ALTA_FORMAZIONE,
                    CATEGORY.PERFEZIONAMENTI
                ].includes(this.category)
            },
            selectedCategory () {
                return this.categories[this.category] ? this.categories[this.category].id : null
            },
            orientatoreSelectable () {
                return this.area === AREA.ORIENTAMENTO
            },
            ecpSelectable () {
                return this.area === AREA.ECP
            },
            categorySelectable () {
                return this.area === AREA.ORIENTAMENTO && this.to_user === 0
            },
            emailRequired () {
                return this.area === AREA.ORIENTAMENTO
            }
        },
        data () {
            return {
                errors: [],
                loading: false,
                modal: false,
                first_name: this.default_first_name,
                last_name: this.default_last_name,
                email: this.default_email,
                telephone: this.default_telephone,
                note: this.default_note,
                category: null,
                area: null,
                subject: null,
                to_user: 0,
                ecp: null,
                areas: [
                    {
                        id: AREA.ORIENTAMENTO,
                        name: 'Orientamento'
                    }, {
                        id: AREA.STATO_IMMATRICOLAZIONE,
                        name: 'Stato Immatricolazione'
                    }, {
                        id: AREA.SUPPORTO_ISCRIZIONE,
                        name: 'Supporto Iscrizione'
                    }, {
                        id: AREA.SUPPORTO_TECNICO,
                        name: 'Supporto Tecnico'
                    }, {
                        id: AREA.TIROCINIO_E_STAGE,
                        name: 'Tirocinio e stage'
                    }, {
                        id: AREA.SEGRETERIA_STUDENTI,
                        name: 'Segreteria Studenti'
                    }, {
                        id: AREA.CONTABILE_FINANZIARIA,
                        name: 'Contabile / Finanziaria'
                    }, {
                        id: AREA.TESI_DI_LAUREA,
                        name: 'Tesi di laurea (assegnazione, contatto docenti, info)'
                    }, {
                        id: AREA.DIDATTICA,
                        name: 'Didattica'
                    }, {
                        id: AREA.VALUTAZIONE_CFU,
                        name: 'Valutazione CFU'
                    }, {
                        id: AREA.TERZO_SETTORE,
                        name: 'Terzo Settore'
                    }, {
                        id: AREA.SANITA,
                        name: 'Area Sanità'
                    }, {
                        id: AREA.ECP,
                        name: 'ECP/EiPoint'
                    }, {
                        id: 11,
                        name: 'Varie'
                    },
                ],
                categories: [
                    {
                        id: CATEGORY.GENERICA,
                        slug: 'generic',
                        name: 'Generica'
                    },
                    {
                        id: CATEGORY.CORSI_DI_LAUREA,
                        slug: 'degree',
                        name: 'Corsi di Laurea'
                    },
                    {
                        id: CATEGORY.MASTER_1_LIVELLO,
                        slug: 'm1l',
                        name: 'Master 1° Livello'
                    },
                    {
                        id: CATEGORY.MASTER_2_LIVELLO,
                        slug: 'm2l',
                        name: 'Master 2° Livello'
                    },
                    {
                        id: CATEGORY.ALTA_FORMAZIONE,
                        slug: 'alfo',
                        name: 'Alta Formazione'
                    },
                    {
                        id: CATEGORY.PERFEZIONAMENTI,
                        slug: 'perf',
                        name: 'Perfezionamenti'
                    },
                    {
                        id: CATEGORY.ESAMI_SINGOLI,
                        slug: 'esami-singoli',
                        name: 'Esami Singoli'
                    },
                    {
                        id: CATEGORY.CFU_24,
                        slug: '24-cfu',
                        name: '24 CFU'
                    }
                ],
                courses: [],
                users: [],
            }
        },
        mounted () {
            this.loading = true
            Promise.all([services.getCourses(),  services.getUsers({group: 1}),  services.getEcps()]).then(([courses, users, ecps]) => {
                this.courses = courses
                this.users = users
                this.ecps = ecps
                this.loading = false
            })
        },
        watch: {
            area () {
                this.category = null
                this.to_user = 0
                this.ecp = null
            },
            to_user (v) {
                if (v !== 0) {
                    this.category = null
                }
            }
        },
        methods: {
            cancel () {
                this.reset()
                this.modal = false
            },
            submit () {
                if (this.category === '') {
                    return this.showErrors({category: ['Categoria non selezionata']})
                }
                this.loading = true
                services.sendCallcenterCall({
                    user_id: LS.get('user').id,
                    to_user_id: this.to_user,
                    direction: 1,
                    area: this.area,
                    category: this.selectedCategory,
                    first_name: this.first_name,
                    last_name: this.last_name,
                    email: this.email,
                    telephone: this.telephone,
                    subject: this.subject,
                    note: this.note,
                    response: 3,
                    ecp: this.ecp
                }).then(response => {
                    this.loading = false
                    if (response) {
                        this.reset()
                        this.modal = false
                        this.$emit('success');
                    }
                }).catch(error => {
                    this.loading = false
                    this.showErrors(error)
                })
            },
            showErrors (error) {
                this.errors = error
                setTimeout(() => {
                    this.errors = []
                }, 4000)
            },
            reset () {
                this.errors = []
                this.area = null
                this.ecp = null
                this.to_user = 0
                this.category = null
                this.subject = null
                this.first_name = ''
                this.last_name = ''
                this.email = ''
                this.telephone = ''
                this.note = ''
            }
        }
    }
</script>