<template>
    <div class="position-absolute p-5 d-flex flex-column position-0">

        <div class="d-flex">
            <div><h4 class="font-weight-bold mb-4">I miei Incontri</h4></div>
            <div class="ml-auto">
                <insert-meeting />
            </div> 
        </div>

        <div class="row">
            <div class="col">
                <div class="bg-white rounded-top-lg shadow-sm">
                    <div class="row p-3">
                        <div class="col-6">
                            <ui-button
                                @click="$router.go(-1)" 
                                color="outline-secondary"
                                size="sm"
                                icon="fas fa-chevron-left"
                                classes="rounded-lg">
                                Torna Indietro
                            </ui-button>
                            <button type="button" class="btn btn-outline-secondary btn-sm rounded-lg px-3" :class="{disabled: loading}" @click="refresh"><i :class="{'fa-spin': loading}" class="fas fa-sync-alt mr-2 pause-spinner"></i>Aggiorna</button>
                        </div>
                        <pager
                            :loading="loading"
                            :total="total"
                            :start="start"
                            :end="end"
                            :prev="prevDisabled"
                            :next="nextDisabled"
                            :pages="pages"
                            :page="page"
                            @prev="goPrev"
                            @next="goNext"
                            @change="p => go(p)"
                        />
                    </div>
                    <div class="bg-gray border-top border-bottom p-3">
                        <div class="row">
                            <div class="col-3">Nome</div>
                            <div class="col-5">Richiesta</div>
                            <div class="col-1">Note</div>
                            <div class="col-2">Operatore</div>
                            <div class="col-1 text-right">Data</div>
                        </div>
                    </div>                    
                </div>
            </div>
        </div>
        <div div style="flex-grow: 1">
            <div class="position-relative slide-wrapper h-100 w-100" style="overflow-x: hidden">
                <horizontal-slide :dir="direction">
                    <ul class="list-group position-absolute left-0 right-0 w-100" :key="page">
                        <li v-if="loading" key="loading" class="rounded-0 list-group-item bg-light text-center">
                            <span v-if="loading">
                                <i class="fas fa-circle-notch fa-spin"></i>
                            </span>
                        </li>
                        <meeting-item
                            v-else-if="meetings.length > 0"
                            @note="note => modal = note"
                            v-for="(meeting, index) in meetings"
                            :key="index"
                            :meeting="meeting"
                        />
                        <li v-else class="rounded-0 list-group-item bg-light text-center">Nessun incontro presente</li>
                    </ul>
                </horizontal-slide>
            </div>
        </div>

        <!-- Modal -->
        <modal :open="modal ? true : false" @close="modal = false" title="Note Incontro">
            <template v-slot:footer>
                <button 
                    type="button" 
                    class="btn btn-outline-secondary rounded-lg" 
                    @click="modal = false">
                    Chiudi
                </button>
            </template>
            <div v-html="modal"></div>
        </modal>

    </div>    
</template>

<script>
    import LS from '../services/localstorage'
    import services from '../services'
    import HorizontalSlide from './transitions/HorizontalSlide'
    import Modal from './layout/Modal'
    import UIButton from './layout/UIButton'
    import InsertMeeting from './modals/InsertMeeting'
    import MeetingItem from './meeting/MeetingItem'

    export default {
        components: { HorizontalSlide, Modal, InsertMeeting, UIButton, MeetingItem },
        props: {
            page: {
                type: Number | String,
                required: false,
                default: 1
            }
        },
        mounted () {
            this.load()
        },
        data () {
            return {
                modal: false,
                start: 0,
                end: 0,
                quantity: 0,
                current_page: 1,
                pages: 0,
                total: 0,
                loading: false,
                meetings: [],
                direction: 1
            }
        },
        methods: {
            goNext() {
                this.$router.push({params: {page: parseInt(this.page) + 1}})
            },
            goPrev() {
                this.$router.push({params: {page: parseInt(this.page) - 1}})
            },
            go (p) {
                this.$router.push({params: {page: parseInt(p)}})
            },
            refresh () {
                if (this.loading) return
                this.load()
            },
            load () {
                this.loading = true
                services.getMeetings({page: this.page, user_id: LS.get('user').id}).then(data => {
                    this.meetings = data.items
                    this.start = data.start + 1
                    this.end = data.end
                    this.current_page = data.page
                    this.pages = data.pages
                    this.total = data.total
                    this.quantity = data.qty
                    this.loading = false
                }).catch(error => {
                    this.loading = false
                })
            }
        },
        watch: {
            page (new_value, prev_value) {
                const diff = prev_value - new_value
                this.direction = diff < 0 ? 1 : 0
                this.load()
            }
        },
        computed: {
            directionId () {
                return this.$options.directions.indexOf(this.call_direction) + 1
            },
            prevDisabled () {
                return !this.prev || this.loading
            },
            nextDisabled () {
                return !this.next || this.loading
            },
            prevPageLink () {
                if (!this.prev) return this.$route.path
                return this.basePath + '/' + this.prev
            },
            nextPageLink () {
                if (!this.next) return this.$route.path
                return this.basePath + '/' + this.next
            },
            basePath () {
                if (this.$route.params && this.$route.params.page) {
                    const index = this.$route.path.lastIndexOf('/')
                    return this.$route.path.substring(0, index)
                }
                return this.$route.path
            },
            next () {
                const next = parseInt(this.page) + 1
                if (next > this.pages) return undefined
                return next
            },
            prev () {
                const prev = parseInt(this.page) - 1
                if (prev === 0) return undefined
                return prev
            }
        }
    }  
</script>