
import services from '../../services'
const state = {
    count: 0,
    loading: false,
    last_id: false,
    lasts: [],
    error: false
}

const mutations = {
    error (state, error) {
        state.error = error
    },
    loading (state, status = false) {
        state.loading = status
    },
    unshift (state, notification) {
        state.lasts.unshift(notification)
        state.count += 1
    },
    push (state, notification) {
        state.lasts.push(notification)
        state.count += 1
    },
    setCount(state, count) {
        state.count = count
    },
    setLastId(state, last_id) {
        state.last_id = last_id
    },
    click (state, id) {
        state.lasts = state.lasts.map(n => {
            if (n.id === id) {
                return {...n, clic_it: 1}
            } else {
                return n
            }
        })
    },
    read (state) {
        state.lasts = state.lasts.map(n => ({...n, read_it: 1}))
        state.count = 0
    }
}

const getters = {
    all: state => state.lasts
}

const actions = {
    get ({ commit, state }) {
        commit('loading', true)
        services.getNotifications({
            last_id: state.last_id
        }).then(data => {
            data.notifications.reverse()
            
            for (let i = data.notifications.length - 1; i > -1; i--) {
                commit('push', data.notifications[i])
            }
            commit('setLastId', data.notifications[0].id)
            if (data.notifications.length < 10) {
                commit('setLastId', false)
            }
            commit('setCount', data.unread)
        }).catch(error => {
            commit('error', error)
        }).then(() => {
            commit('loading', false)
        })
    },
    click ({ commit }, id) {
        commit('click', id)
        services.clickNotification(id).then(data => true).catch(() => false)
    },
    read ({ commit }) {
        commit('read')
        services.readNotifications().then(data => true).catch(() => false)
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}