

import Vue from 'vue'
import services from '../../services'
const state = {
    requests: {
        start: 0,
        end: 0,
        quantity: 0,
        page: 1,
        pages: 0,
        total: 0,
        loading: false,
        items: []
    },
    loading: false,
    error: false,
    current: null
}

const mutations = {
    set (state, data) {
        state.requests.items = data.items
        state.requests.start = data.start
        state.requests.end = data.end
        state.requests.page = data.page
        state.requests.pages = data.pages
        state.requests.total = data.total
        state.requests.qty = data.qty
    },
    loading (state, status = true) {
        state.loading = status
    },
    error (state, error) {
        state.error = error
    },
    current (state, request) {
        state.current = request
    },
    busy (state, { id, user_id }) {
        state.requests.items = state.requests.items.map(item => {
            if (item.id === id) {
                return {
                    ...item,
                    user_id,
                    busy: true
                }
            }
            return item
        })
    },
    change_status (state, {id, status, status_class, status_icon, status_name}) {
        /*
        const index = state.requests.items.findIndex(item => item.id == id)
        Vue.set(state.requests.items[index], {
            ...state.requests.items[index],
            status,
            status_class,
            status_icon,
            status_name
        })
        return
        */
        state.requests.items = state.requests.items.map(item => {
            if (item.id == id) {
                return {
                    ...item,
                    status,
                    status_class,
                    status_icon,
                    status_name
                }
            }
            return item
        })
    }
}
const actions = {
    get ({ commit }, id) {
        commit('loading', true)
        services.getRequest(id).then(data => {
            commit('current', data)
        }).catch(error => {
            commit('error', error)
        }).then(() => {
            commit('loading', false)
        })
    },
    getAll ({ commit }, params) {
        commit('loading', true)
        services.getRequests(params).then(data => {
            commit('set', data)
        }).catch(error => {
            commit('error', error)
        }).then(() => {
            commit('loading', false)
        })
    }
}

const getters = {
    all: state => state.requests,
    notWorked: state => {
        return {
            ...state,
            items: state.requests.items.filter(req => req.user_id !== 0)
        }
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}