<template>
    <div class="card rounded-0 no-print position-relative">
        <div class="card-header bg-white">
            <h5 v-if="senderType === 'user' && !support">Rispondi a {{ name }} ({{ email }})</h5>
            <h5 v-else-if="support">Rispondi alla richiesta di intervento</h5>
            <h5 v-else>Invia una risposta</h5>
        </div>
        <div class="card-body">
            <div class="row no-print position-relative">
                <div class="col">
                    <editor :disabled="loading" tag="" v-model="content"></editor>
                    <file-dropper @files="files => (attachments = files)" />
                </div>
            </div>
            <div class="row no-print mt-2">
                <div class="col">
                    <ui-button @click="answer" color="crm" icon="far fa-envelope" :loading="loading">Rispondi</ui-button>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import FileDropper from '../FileDropper'
    import services from '../../services'

    export default {
        name: 'request-input',
        components: { FileDropper },
        props: {
            name: {
                type: String,
                required: true
            },
            email: {
                type: String,
                required: true
            },
            rid: {
                type: Number,
                required: false
            },
            cuid: {
                type: Number,
                required: false
            },
            senderType: {
                validator: value => {
                    return ['user', 'contact'].indexOf(value) > -1
                }
            },
            token: {
                type: String,
                required: false
            },
            support: {
                required: false,
                default: null
            }
        },
        data () {
            return {
                content: '',
                attachments: [],
                loading: false
            }
        },
        methods: {
            answer () {
                if (this.support) return this.answerToSupport()
                return (this.senderType === 'user') ? this.answerPrivate() : this.answerPublic()
            },
            answerToSupport () {
                this.loading = true
                services.sendSupportAnswer({
                        support_id: this.support.id,
                         ...this.answerData
                    }).then(res => {
                    this.$emit('sent', this.anwerData)
                    this.content = ''
                    this.attachments.splice(0, this.attachments.length)
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading = true
                })
            },
            answerPublic () {
                this.loading = true
                services.sendPublicAnswer(this.token, this.answerData).then(res => {
                    this.$emit('sent', this.answerData)
                    this.content = ''
                    this.attachments.splice(0, this.attachments.length)
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading = false
                })
            },
            answerPrivate () {
                this.loading = true
                services.sendAnswer(this.answerData).then(res => {
                    this.$emit('sent', this.answerData)
                    this.content = ''
                    this.attachments.splice(0, this.attachments.length)
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading = false
                })
            }
        },
        computed: {
            answerData () {
                return {
                    rid: this.rid,
                    cuid: this.cuid,
                    content: this.content,
                    sender_type: this.senderType,
                    attachments: this.attachments
                }
            }
        }
    }
</script>
<style>
    .tox-notifications-container, .tox-statusbar__branding {
        display:none!important;
    }
</style>