<template>
   <div class="position-absolute p-5 d-flex flex-column position-0"> 

        <div class="d-flex">
            <div><h4 class="font-weight-bolder mb-4">Utenti</h4></div> 
            <div class="ml-auto">
                <ui-button 
                    @click="closed = !closed" 
                    color="success"
                    size="sm"
                    classes="rounded-lg"
                    :icon="`fas ${closed ? 'fa-plus' : 'fa-minus'}`">
                    Crea Utente
                </ui-button>
            </div>
        </div>

        <div :class="classes">
            <div class="card-header bg-white">
                <h5 class="m-0">Crea Utente</h5>
            </div>
            <div class="card-body">
                 
                <div class="form-group row">
                    <label for="first_name" class="col-sm-2 col-form-label text-right">Nome</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control rounded-lg" name="first_name" placeholder="Nome" id="first_name" required="" v-model="first_name">
                    </div>
                </div>
                
                <div class="form-group row">
                    <label for="last_name" class="col-sm-2 col-form-label text-right">Cognome</label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control rounded-lg" name="last_name" placeholder="Cognome" id="last_name" required="" v-model="last_name">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="email" class="col-sm-2 col-form-label text-right">E-Mail</label>
                    <div class="col-sm-10">
                        <input type="email" class="form-control rounded-lg" name="email" placeholder="E-Mail" id="email" required="" v-model="email">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="group" class="col-sm-2 col-form-label text-right">Gruppo</label>
                    <div class="col-sm-10">
                        <select class="custom-select rounded-lg" id="group" name="group" required="" v-model="role">
                            <option value="1">Orientamento</option>
                            <option value="2">Valutazione CFU</option>
                            <option value="7">Call Center</option>
                            <option value="6">Admin</option>
                        </select>
                    </div>
                </div>
                    
                <div class="form-group row">
                    <label for="password" class="col-sm-2 col-form-label text-right">Password</label>
                    <div class="col-sm-10">
                        <input type="password" class="form-control rounded-0" name="password" placeholder="Password" id="password" required="" v-model="password">
                    </div>
                </div> 

                <div class="form-group row">
                    <label for="repeat_password" class="col-sm-2 col-form-label text-right">Ripeti Password</label>
                    <div class="col-sm-10">
                        <input type="password" class="form-control rounded-lg" name="repeat_password" placeholder="Ripeti Password" id="repeat_password" required="" v-model="repeat_password">
                    </div>
                </div>
        
                <div class="form-group row">
                    <label for="photo" class="col-sm-2 col-form-label text-right">Foto</label>
                    <div class="col-sm-10">
                        <input type="file" name="photo" placeholder="Photo" id="photo" accept="image/*" required="" @change="fileChange">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="photo" class="col-sm-2 col-form-label text-right"></label>
                    <div class="col-sm-10">
                        <ui-button 
                            class="rounded-lg" 
                            @click="send" :loading="loading" 
                            icon="fas fa-plus">Crea
                        </ui-button>
                    </div>
                </div>

            </div>
        </div>

        <div class="row">
            <div class="col">
                <div class="bg-white rounded-top-lg shadow-sm">
                    <div class="row p-3">
                        <div class="col-6">
                            <ui-button
                                @click="$router.go(-1)" 
                                color="outline-secondary"
                                size="sm"
                                icon="fas fa-chevron-left"
                                classes="rounded-lg">
                                Torna Indietro
                            </ui-button>
                        </div>
                        <pager
                            :loading="loading"
                            :total="total"
                            :start="start"
                            :end="end"
                            :prev="prevDisabled"
                            :next="nextDisabled"
                            :pages="pages"
                            :page="page"
                            @prev="goPrev"
                            @next="goNext"
                            @change="p => go(p)"
                        />
                    </div>
                    <div class="bg-gray border-top border-bottom p-3 text-uppercase small">
                        <div class="row">
                            <div class="col-5">Operatore</div>
                            <div class="col-4">E-Mail</div>
                            <div class="col-3">Azioni</div>
                        </div>
                    </div>                    
                </div>
            </div>
        </div>
        
        <div class="flex-grow-1">
            <div class="position-relative h-100 w-100 overflow-x-hidden overflow-y-auto rounded-bottom-lg">
                <horizontal-slide :dir="direction">
                    <ul class="list-group position-absolute w-100 shadow-sm" :key="page">
                        <li class="rounded-0 list-group-item" v-for="(user, index) in users" :key="index">
                            <div class="row align-items-center">
                                <div class="col-5">
                                    <div class="d-flex align-items-center"> 
                                        <div class="mr-3">
                                            <img :src="`https://crm.unimercatorum.it/uploads/users/${user.photo}`" height="50" class="rounded-circle">
                                        </div>
                                        <div class="d-flex flex-column">
                                            <div class="h5 m-0">{{ user.first_name }} {{ user.last_name }}</div>
                                            <div class="text-muted">{{ user.group_name }}</div> 
                                        </div>
                                    </div>
                                </div>
                                <div class="col-4">{{ user.email }}</div>
                                <div class="col-3">
                                    <ui-button
                                        color="outline-secondary"
                                        class="rounded-lg"
                                        icon="fas fa-edit"
                                        size="sm">
                                        Modifica
                                    </ui-button>
                                    <ui-button
                                        color="outline-secondary"
                                        class="rounded-lg"
                                        icon="fas fa-times"
                                        size="sm">
                                        Elimina
                                    </ui-button>
                                </div>
                            </div>
                        </li>
                    </ul>
                </horizontal-slide> 
            </div>
        </div>

    </div>
</template>
<script>
    import services from '../services'

    export default {
        data () {
            return {
                first_name: '',
                last_name: '',
                email: '',
                role: 1,
                password: '',
                repeat_password: '',
                photo: null,
                closed: true,
                start: 0,
                end: 0,
                quantity: 0,
                current_page: 1,
                pages: 0,
                total: 0,
                loading: false,
                users: [],
            }
        },
        roles: [
            'Operatore',
            'Amministratore'
        ],
        methods: {
            load () {
                this.loading = true
                services.getUsers().then(data => {
                    this.start = data.start + 1
                    this.end = data.end
                    this.current_page = data.page
                    this.pages = data.pages
                    this.total = data.total
                    this.quantity = data.qty
                    this.loading = false
                    this.users = data.items
                }).catch(error => {
                    this.loading = false
                })
            },
            goNext() {
                this.$router.push({params: {page: parseInt(this.page) + 1}})
            },
            goPrev() {
                this.$router.push({params: {page: parseInt(this.page) - 1}})
            },
            go (p) {
                this.$router.push({params: {page: parseInt(p)}})
            },
            fileChange (e) {
                this.photo = e.target.files[0]
            },
            send () {
                this.loading = true
                services.createUser({
                    first_name: this.first_name,
                    last_name: this.last_name,
                    email: this.email,
                    role: this.role,
                    password: this.password,
                    repeat_password: this.repeat_password,
                    photo: this.photo
                }).catch(error => {
                    alert(error)
                }).then(data => {
                    this.loading = false
                    console.log(data)
                })
            },
            roleName (role) {
                return this.$options.roles[role]
            }
        },
        mounted () {
            this.load()
        },
        computed: {
            classes () {
                return {
                    'card': true,
                    'rounded-lg': true,
                    'shadow-sm': true,
                    'border-0': true,
                    'mb-3': true,
                    'd-none': this.closed
                }
            },
            prevDisabled () {
                return !this.prev || this.loading
            },
            nextDisabled () {
                return !this.next || this.loading
            },
            prevPageLink () {
                if (!this.prev) return this.$route.path
                return this.basePath + '/' + this.prev
            },
            nextPageLink () {
                if (!this.next) return this.$route.path
                return this.basePath + '/' + this.next
            },
            basePath () {
                if (this.$route.params && this.$route.params.page) {
                    const index = this.$route.path.lastIndexOf('/')
                    return this.$route.path.substring(0, index)
                }
                return this.$route.path
            },
            next () {
                const next = parseInt(this.page) + 1
                if (next > this.pages) return undefined
                return next
            },
            prev () {
                const prev = parseInt(this.page) - 1
                if (prev === 0) return undefined
                return prev
            }
        }
    }
</script>