<template>
    <div class="h-100" style="overflow-y: auto">
        <div class="container my-5">
            <div class="row h-100 justify-content-center align-items-center" v-if="request">
                <div class="col-lg-12">
                    <div class="card rounded-0">
                        <div class="card-body p-lg-5">
                            <div class="d-md-flex">
                                <div class="text-center mb-4 mr-4"> 
                                    <a :href="websiteUri" title="Vai al sito ufficiale"><img src="https://www.unimercatorum.it/app/templates/public/default/assets/images/logo.png" width="150"></a>
                                </div>
                                <div>
                                    <h2 class="font-weight-bold mb-3">Richiesta Informazioni</h2>
                                    <p class="h5 mb-4">
                                        Gentile <strong>{{ request.first_name }} {{ request.last_name }}</strong>,<br>
                                        in questa pagina potrai controllare lo stato della tua richiesta e consultare le risposte del nostro Ufficio Orientamento.
                                        Utilizza il modulo "Rispondi" per richiedere ulteriori informazioni.
                                    </p>
                                </div>
                            </div>
                            <div class="card rounded-0 mb-4">
                                <div class="card-header bg-white">
                                    <h5 class="m-0">Ticket</h5>
                                </div>
                                <div class="card-body">
                                    <div class="row pb-3 mb-3 border-bottom">
                                        <div class="col">
                                            <div class="d-block"><i class="fa fa-tag mr-2"></i>Tipologia</div> 
                                            <div class="ml-4 font-weight-bold">{{ request.type_name }}</div>
                                        </div>
                                        <div class="col">
                                            <div class="d-block"><i class="fas fa-star mr-2"></i>Categoria</div> 
                                            <div class="ml-4 font-weight-bold">{{ request.category_name }}</div>
                                        </div>
                                        <div class="col">
                                            <div class="d-block"><i class="fas fa-info-circle mr-2"></i>Oggetto</div> 
                                            <div class="ml-4 font-weight-bold">{{ request.subject }}</div>
                                        </div>
                                        <div class="col">
                                            <div class="d-block"><i class="fas fa-desktop mr-2"></i>Provenienza</div> 
                                            <div class="ml-4 font-weight-bold">{{ textProvider }}</div>
                                        </div>
                                    </div>
                                    <div class="row">
                                        <div class="col">
                                            <div class="d-block"><i class="far fa-envelope mr-2"></i>Status ticket</div> 
                                            <div class="ml-4 font-weight-bold text-success">{{ request.status_name }}</div>
                                        </div>
                                        <div class="col">
                                            <div class="d-block"><i class="far fa-user-circle mr-2"></i>Operatore</div> 
                                            <div class="ml-4 font-weight-bold">{{ request.user_first_name}} {{ request.user_last_name }}</div>
                                        </div>
                                        <div class="col">
                                            <div class="d-block"><i class="far fa-calendar mr-2"></i>Ticket aperto</div> 
                                            <div class="ml-4 font-weight-bold"><date-time :at="request.created_at" /></div>
                                        </div>
                                        <div class="col">
                                            <template v-if="ticketClosed">
                                                <div class="d-block"><i class="far fa-calendar mr-2"></i>Ticket chiuso</div> 
                                                <div class="ml-4 font-weight-bold"><date-time :at="request.updated_at" /></div>
                                            </template>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <ul class="list-group">
                                <request-question 
                                    :request="request"
                                    :link="false"
                                />
                                <request-answer
                                    v-for="(reply, index) in request.replies"
                                    :key="index"
                                    :uid="reply.user_id"
                                    :name="`${reply.first_name} ${reply.last_name}`"
                                    :email="reply.email"
                                    :datetime="reply.created_at"
                                    :body="reply.body"
                                    :attachments="reply.attachments"
                                    :senderType="reply.sender_type"
                                    :link="false"
                                    :token="token"
                                    :photo="reply.photo"
                                />
                                <request-input
                                    v-if="answerable"
                                    :token="token"
                                    :name="`${request.user_first_name} ${request.user_last_name}`"
                                    :email="request.email"
                                    :rid="request.id"
                                    :loading="loading"
                                    senderType="contact"
                                    @sent="answered"
                                />
                            </ul>
                        </div>
                    </div>
                </div> 
            </div> 
        </div>
    </div>
</template>

<script>
    import services from '../services'
    import RequestAnswer from './request/RequestAnswer'
    import RequestInput from './request/RequestInput'
    import RequestQuestion from './request/RequestQuestion'

    export default {
        name: 'public-request',
        components: { RequestAnswer, RequestInput, RequestQuestion },
        data () {
            return {
                request: false,
                loading: false
            }
        },
        props: {
            token: {
                type: String,
                required: true
            }
        },
        mounted () {
            this.load()
        },
        methods: {
            answered () {
                this.load()
            },
            load () {
                services.getPublicRequest(this.token).then(data => {
                    this.request = data
                }).catch(error => {
                    alert(error)
                }).then(() => {

                })
            }
        },
        computed: {
            answerable () {
                return !this.ticketClosed && this.request.status !== 2
            },
            ticketClosed () {
                return this.request.status ===  6 || this.request.status === 5
            },
            websiteUri () {
                return services.website_uri
            },
            textProvider() {
                switch (this.request.provider) {
                    case 'website':
                        return 'Sito Web';
                    default: 
                        return this.request.provider;
                }
            }
        }
    }
</script>