<template>
    <div class="cursor-pointer d-flex px-2 py-1 chat-user align-items-center">
        <div class="mr-2">
            <img :src="photo" :class="photoClass" style="height:36px;width:36px; border-radius:50%">
        </div>
        <div class="flex-grow-1 d-flex flex-column" :class="textClass">
            <div>{{ first_name }} {{ last_name }} <span v-if="typing">...</span></div>
            <div><small>{{ group }}</small></div>
        </div>
        <div class="text-right">
            <span :class="badgeClass" v-if="messages_count > 0">{{ messages_count }}</span>
            <small><i :class="iconClass"></i></small>
        </div>
    </div>
</template>
<script>
    
    import store from '../../store'

    export default {
        name: 'chat-user',
        props: {
            id: {
                type: Number,
                required: true
            },
            first_name: {
                type: String,
                required: true
            },
            photo: {
                type: String,
                requred: true
            },
            last_name: {
                type: String,
                required: true
            },
            online: {
                type: Boolean,
                required: false,
                default: false
            },
            messages_count: {
                type: Number,
                required: false,
                default: 0
            },
            typing: {
                type: Boolean,
                required: false,
                default: false
            },
            read: {
                type: Boolean,
                required: false,
                default: false
            },
            group: {
                type: String,
                required: true
            }
        },
        typingTimeout: null,
        watch: {
            typing (n, o) {
                if (n) {
                    clearTimeout(this.$options.typingTimeout)
                    this.$options.typingTimeout = setTimeout(() => {
                        store.commit('chat/unset_user_typing', this.id)
                    }, 3000)
                }
            }
        },
        computed: {
            photoClass()
            {
                return {
                    //'photo'                : true,
                    //'photo-online-read'    : this.online && this.read,
                    //'photo-offline-read'   : !this.online && this.read,
                    //'photo-online-unread'  : this.online && !this.read,
                    //'photo-offline-unread' : !this.online && !this.read
                }
            },
            textClass ()
            {
                return {
                    'text-success': this.typing,
                    'font-italic': this.typing
                }
            },
            iconClass ()
            {
                return {
                    online: true,
                    'fas fa-circle': true,
                    'font-weight-bold': true,
                    'mr-2': true,
                    small: true,
                    'text-success': this.online,
                    'text-secondary': !this.online
                }
            }, 
            badgeClass ()
            {
                return {
                    badge: true,
                    'badge-danger': true,
                    animated: true,
                    flash: true
                }
            }
        }
    }
</script>
<style scoped>
    .chat-user:hover {
        background:#b8bcc1;
    }
    .photo {
        border-width: 2px;
        border-style: solid;
    }
    .photo-online-read {
        border-color: #28a745;
    }
    .photo-offline-read {
        border-color: #748396;
    }
    .photo-online-unread {
        border-color: #FFC107;
    }
    .photo-offline-unread {
        border-color: #748396;
    }

</style>