<template>
    <div style="display:inline-block">
        <ui-button icon="fa fa-phone" color="warning" @click="modal = true" class="btn-inline rounded-lg" size="sm">Richiedi Intervento</ui-button>
        <modal :open="modal" @close="modal = false" size="lg" title="Richiedi Intervento">
            <template v-slot:footer>
                <ui-button icon="fa fa-times" @click="cancel" color="danger">Annulla</ui-button>
                <ui-button icon="fa fa-phone" color="success" :loading="loading" @click="submit">Richiedi Intervento</ui-button>
            </template>
            <form ref="form">
                <div class="form-group row">
                    <label for="incoming_call_subject" class="col-sm-2 col-form-label text-right">Reparto</label>
                    <div class="col-sm-10">
                        <select v-model="to_group_id" class="form-control" required>
                            <option value="0">Seleziona Reparto</option>
                            <option v-for="group in groups" :value="group.id">{{ group.name }}</option>
                        </select>
                    </div>
                </div>
                <div class="form-group row">
                    <label for="incoming_call_note" class="col-sm-2 col-form-label text-right">Note</label>
                    <div class="col-sm-10">
                        <editor :disabled="loading" id="note" tag="" v-model="note"></editor>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-2"></div>
                    <div class="col-sm-10">
                        <ul class="p-0">
                            <li class="text-danger" v-for="error in errors">{{ error.join(',') }}</li>
                        </ul>        
                    </div>
                </div>
            </form>
        </modal>
    </div>
</template>
<script>
    import services from '../../services'
    import LS from '../../services/localstorage'
    import Modal from '../layout/Modal'

    export default {
        components: { Modal },
        name: 'send-support',
        computed: {
            selectableUsers () {
                return this.users.filter(u => u.id != LS.get('user').id)
            }
        },
        props: {
            request_id: {
                type: String,
                reqired: true
            }
        },
        data () {
            return {
                errors: [],
                loading: false,
                modal: false,
                groups: [
                    {
                        id: 2,
                        name: 'Valutazione Carriere'
                    }
                ],
                to_group_id: 0,
                note: ''
            }
        },
        methods: {
            cancel () {
                this.reset()
                this.modal = false
            },
            submit () {
                if (this.user === '') {
                    return this.showErrors({to_group_id: ['Utente non selezionato']})
                }
                this.loading = true
                services.sendSupport({
                    rid: this.request_id,
                    uid: LS.get('user').id,
                    togroup: this.to_group_id,
                    note: this.note
                }).then(response => {
                    this.loading = false
                    if (response) {
                        this.reset()
                        this.modal = false
                        this.$emit('sent')
                    }
                }).catch(error => {
                    this.loading = false
                    this.showErrors(error)
                })
            },
            showErrors (error) {
                this.errors = error
                setTimeout(() => {
                    this.errors = []
                }, 4000)
            },
            reset () {
                this.errors = []
                this.note = ''
                this.to_group_id = 0
            }
        }
    }
</script>