<template>
    <div>
        <input type="file" ref="input" @change="change" multiple>
        <div class="my-3">
            <ui-button @click="remove(index)" color="secondary" @mouseover.native="icon = 'fas fa-times'" @mouseout.native="icon = 'fas fa-paperclip'" v-for="(file, index) in files" :key="index" rounded outline size="sm" :icon="icon">{{ file.name }}</ui-button>
        </div>
        <div @click="click" ref="filedrop" :class="`p-4 text-center mb-2 border-dashed filedrop ${background}`">
            <h5 class="text-muted"><strong>Clicca o trascina qui i file per aggiungere allegati</strong></h5>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'file-dropper',
        props: {},
        data () {
            return {
                capable: false,
                loading: false,
                files: [],
                counter: 0,
                icon: 'fas fa-paperclip'
            }
        },
        mounted () {
            this.capable = this.isDnDCapable()
            this.initEvents()
        },
        methods: {
            add (files) {
                files.forEach(file => {
                    this.files.push(file)
                })
                this.$emit('files', this.files)
            },
            remove (index) {
                this.files.splice(index, 1)
                this.$emit('files', this.files)
            },
            change (e) {
                this.add([...this.$refs.input.files])
            },
            click (e) {
                this.$refs.input.click()
            },
            isDnDCapable () {
                var div = document.createElement('div');
                return ( ( 'draggable' in div )
                        || ( 'ondragstart' in div && 'ondrop' in div ) )
                        && 'FormData' in window
                        && 'FileReader' in window
            },
            initEvents () {
                if(this.capable) {
                    ['drag', 'dragstart', 'dragend', 'dragover', 'dragenter', 'dragleave', 'drop'].forEach(evt => {
                        this.$refs.filedrop.addEventListener(evt, e => {
                            e.preventDefault()
                            e.stopPropagation()
                        }, false)
                    })

                    this.$refs.filedrop.addEventListener('dragenter', e => {
                        e.preventDefault()
                        this.counter++
                    })
                    this.$refs.filedrop.addEventListener('dragleave', e => {
                        e.preventDefault()
                        this.counter--
                    })

                    this.$refs.filedrop.addEventListener('drop', e => {
                        this.add([...e.dataTransfer.files])
                    })
                }
            }
        },
        computed: {
            background () {
                return this.hover ? 'bg-white' : 'bg-light'
            },
            hover () {
                return this.counter > 0
            }
        }
    }
</script>
<style scoped>
    .filedrop {
        cursor: pointer;
    }
    .filedrop.active {
        background: 
    }
    .border-dashed {
        border: 2px dashed #ccc;
    }
    input {
        display:none;
    }
</style>