import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
import LS from '../services/localstorage'

import Home from '../components/Home'
import Login from '../components/Login'
import Inbox from '../components/Inbox'
import Request from '../components/Request'
import PublicRequest from '../components/PublicRequest'
import Contact from '../components/Contact'
import Contacts from '../components/Contacts'
import Recall from '../components/Recall'
import Users from '../components/Users'
import Calls from '../components/Calls'
import Meeting from '../components/Meeting'
import SupportInbox from '../components/SupportInbox'
import Callcenter from '../components/Callcenter'
import NotFound from '../components/NotFound'

Vue.use(Router)
const router = new Router({
    mode: 'history',
    routes: [
        {
            path: '/ticket/:token',
            name: 'PublicRequest',
            component: PublicRequest,
            props: true
        },
        {
            path: '/login',
            name: 'Login',
            component: Login
        }, {
            path: '/',
            name: 'Home',
            component: Home,
            children: [
                {
                    path: '/contact/:id',
                    component: Contact,
                    props: true
                },
                {
                    path: '/contacts/:page?',
                    component: Contacts,
                    props: true
                },
                {
                    path: '/calls/:call_direction/:page?',
                    component: Calls,
                    props: route => {
                        return {
                            call_direction: route.params.call_direction,
                            page: route.params.page,
                            own: false
                        }
                    }
                },
                {
                    path: '/user/meeting/:page?',
                    component: Meeting,
                    props: true
                },
                {
                    path: '/user/calls/assigned/:page?',
                    component: Calls,
                    meta: {
                        title: 'Telefonate da lavorare'
                    },
                    props: true
                },
                {
                    path: '/user/calls/:call_direction/:page?',
                    component: Calls,
                    props: route => {
                        return {
                            call_direction: route.params.call_direction,
                            page: route.params.page,
                            own: true
                        }
                    }
                },
                {
                    path: '/users',
                    component: Users
                },
                {
                    path: '/recall/:page?',
                    component: Recall,
                    props: true
                },
                {
                    path: '/request/:id',
                    component: Request,
                    props: true
                }, 
                {
                    path: '/towork/:page?',
                    component: Inbox,
                    meta: {
                        title: 'Richieste da lavorare'
                    },
                    props: route => {
                        return {
                            status: 'towork',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/replied/:page?',
                    component: Inbox,
                    props: route => {
                        return {
                            status: 'replied',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/worked/:page?',
                    component: Inbox,
                    meta: {
                        title: 'Richieste lavorate'
                    },
                    props: route => {
                        return {
                            status: 'worked',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/assigned/:page?',
                    component: Inbox,
                    meta: {
                        title: 'Richieste in Lavorazione'
                    },
                    props: route => {
                        return {
                            status: 'assigned',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/enrol/:page?',
                    component: Inbox,
                    meta: {
                        title: 'Iscrizioni Online'
                    },
                    props: route => {
                        return {
                            type: 4,
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/try/:page?',
                    component: Inbox,
                    meta: {
                        title: 'Prova la Piattaforma'
                    },
                    props: route => {
                        return {
                            type: 2,
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/user/assigned/:page?',
                    component: Inbox,
                    meta: {
                        title: 'Le mie Richieste in lavorazione'
                    },
                    props: route => {
                        return {
                            user: LS.get('user').id,
                            status: 'assigned',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/user/cfu/:page?',
                    component: Inbox,
                    props: route => {
                        return {
                            user: LS.get('user').id,
                            status: 'assigned',
                            type: 3,
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/user/replied/:page?',
                    component: Inbox,
                    meta: {
                        title: 'Le mie Richieste con risposta'
                    },
                    props: route => {
                        return {
                            user: LS.get('user').id,
                            status: 'replied',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/user/worked/:page?',
                    component: Inbox,
                    meta: {
                        title: 'Le mie Richieste lavorate'
                    },
                    props: route => {
                        return {
                            user: LS.get('user').id,
                            status: 'worked',
                            page: route.params.page
                        }
                    }
                }, {
                    path: '/category/:category/:page?',
                    component: Inbox,
                    props: true
                }, {
                    path: '/provider/:provider/:page?',
                    component: Inbox,
                    props: true
                }, {
                    path: 'inbox/:page?',
                    component: Inbox,
                    meta: {
                        title: 'In Arrivo'
                    },
                    props: route => {
                        return {
                            status: 'inbox',
                            page: route.params.page
                        }
                    }
                }, {
                    path:'support/inbox/:page?',
                    component: SupportInbox,
                    meta: {
                        title: 'In Arrivo'
                    },
                    props: route => {
                        return {
                            group: 2,
                            done: 0,
                            own: 0,
                            page: route.params.page
                        }
                    }
                }, {
                    path:'support/worked/:page?',
                    component: SupportInbox,
                    props: route => {
                        return {
                            group: 2,
                            done: 1,
                            page: route.params.page
                        }
                    }
                }, {
                    path:'support/user/inbox/:page?',
                    component: SupportInbox,
                    meta: {
                        title: 'Le mie Valutazioni CFU da lavorare'
                    },
                    props: route => {
                        return {
                            group: 2,
                            done: 0,
                            own: 1,
                            page: route.params.page
                        }
                    }
                }, {
                    path:'support/user/worked/:page?',
                    component: SupportInbox,
                    meta: {
                        title: 'Le mie Valutazioni CFU lavorate'
                    },
                    props: route => {
                        return {
                            group: 2,
                            done: 1,
                            own: 1,
                            status: 'worked',
                            page: route.params.page
                        }
                    }
                }, {
                    path: 'callcenter/:page?',
                    component: Callcenter,
                    meta: {
                        title: 'Homepage Centralino'
                    },
                    props: route => {
                        return {
                            page: route.params.page,
                            user: route.params.user,
                        }
                    }
                }
            ]
        },
        { path: "*", component: NotFound }
    ],
    watch: {
        $route(to, from) {
            console.log(to)
        }
    }
})

window.popStateDetected = false
window.addEventListener('popstate', () => {
    window.popStateDetected = true
})

router.beforeEach((to, from, next) => {
    const authRequired = matchProtectedRoute(to.path)
    const loggedIn = LS.get('user')
    store.commit('page/set_title', to.meta.title)
    if (authRequired && !loggedIn) {
        return next('/login')
    }
    if (to.path === '/') {
        if (loggedIn.group === 1) {
            return next('/inbox')
        } else if (loggedIn.group === 2) {
            return next('/support/inbox')
        } else if (loggedIn.group === 7) {
            return next('/callcenter')
        }
    }
    next()
})

const publicRoutes = [
    /\/login/i,
    /\/ticket\/\w+/i
]

const matchProtectedRoute = path => {
    for (let i in publicRoutes) {
        let pr = publicRoutes[i]
        if (pr.test(path)) {
            return false
        }
    }
    return true
}

export default router