<template>
    <li class="rounded-0 pointer bg-white border-bottom px-3 py-2" @click="$emit('click', call.note)">
        <div class="row align-items-center">
            <div class="col-3 text-capitalize" v-if="showName">
                <router-link :to="`/contact/${call.contact_id}`">{{ `${call.contact_first_name} ${call.contact_last_name}` }}</router-link>
            </div>
            <div :class="{
                'col-4': showName,
                'col-7': !showName,
                'text-truncate': true
            }">
                <span :class="directionClass">{{ directionName }}</span>
                <span class="btn btn-xs btn-outline-secondary mr-1 rounded-lg" v-if="call.category_name">{{ call.category_name }}</span>
                <span class="text-muted">{{ (call.subject) ? call.subject : 'Generica' }}</span>
            </div>
            <div class="col-1 small text-truncate">
                <button 
                    class="btn btn-xs btn-block btn-outline-secondary mr-1 rounded-lg" 
                    @click="$emit('note', call.note)"
                    v-if="call.note != ''">
                    <i class="far fa-file-alt mr-2"></i>Note
                </button>
            </div>
            <div class="col-3 text-left">
                <div class="d-flex align-items-center">
                    <div class="w-50 pr-2 small">
                        <i class="far fa-user-circle mr-2"></i>{{ userName }}
                    </div>
                    <div class="w-50 small text-center">
                        {{ responseName }}
                    </div>
                </div>
            </div>
            <div class="col-1 text-right text-muted small">
                <date-time :at="call.created_at" />
            </div>
        </div>
    </li>
</template>

<script>
    export default {
        props: {
            call: {
                type: Object,
                required: true
            },
            showName: {
                type: Boolean,
                required: false,
                default: true
            }
        },
        directions: {
            '1': 'In Entrata',
            '2': 'In Uscita'
        },
        responses: {
            '1': 'Nessuna Risposta',
            '2': 'Non Interessato',
            '3': 'Poco Interessato',
            '4': 'Molto Interessato',
            '5': 'Da Ignorare'
        },
        computed: {
            directionClass () {
                return {
                    'btn': true,
                    'btn-xs': true,
                    'mr-1': true,
                    'rounded-lg': true,
                    'text-white': true,
                    'bg-success': this.call.direction == 1,
                    'bg-danger': this.call.direction == 2
                }
            },
            responseName () {
                return this.$options.responses[this.call.response]
            },
            directionName () {
                return this.$options.directions[this.call.direction]
            },
            contactName () {
                return this.call.contact_first_name + ' ' + this.call.contact_last_name
            },
            userName () {
                return this.call.user_first_name.substring(0, 1) + '. ' + this.call.user_last_name
            }
        }
    }
</script>

<style scoped>
    li.disabled {
        opacity: .5;
        transition: all .3s ease;
    }
    .list-group .btn-xs, table .btn-xs {
        padding: 0 .5rem;
        font-size: .875rem;
        line-height: 1.5;
        border-radius: .2rem;
    }
    * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
</style>