import Vue from 'vue'
import LS from '../../services/localstorage'

const state = {
    messages: [],
    users: [],
    online: false
}

const mutations = {
    set_user_typing (state, user_id) {
        const index = state.users.findIndex(u => u.id == user_id)
        Vue.set(state.users, index, {
            ...state.users[index],
            typing: true
        })
    },
    set_chat_as_read (state, user_id) {
        const index = state.users.findIndex(u => u.id == user_id)
        Vue.set(state.users, index, {
            ...state.users[index],
            read: true
        })
    },
    set_chat_as_unread (state, user_id) {
        const index = state.users.findIndex(u => u.id == user_id)
        Vue.set(state.users, index, {
            ...state.users[index],
            read: false
        })
    },
    unset_user_typing (state, user_id) {
        const index = state.users.findIndex(u => u.id == user_id)
        Vue.set(state.users, index, {
            ...state.users[index],
            typing: false
        })
    },
    set_messages_as_read (state, payload) {
        for (let i = 0; i < state.messages.length; i++) {
            if (state.messages[i].sender == payload.id && !state.messages[i].read_at) {
                Vue.set(state.messages, i, {
                    ...state.messages[i],
                    read_at: payload.read_at
                })
            }
        }
    },
    set_sent_messages_as_read (state, payload) {
        for (let i = 0; i < state.messages.length; i++) {
            if (state.messages[i].recipient == payload.id && !state.messages[i].read_at) {
                Vue.set(state.messages, i, {
                    ...state.messages[i],
                    read_at: payload.read_at
                })
            }
        }
    },
    history (state, messages) {
        messages = messages.map(message => {
            return message
        })
        for (let i = 0; i < messages.length; i++) {
            if (state.messages.findIndex(m => m.id == messages[i].id) > -1) continue
            state.messages.unshift(messages[i])
        }
    },
    set_users (state, users) {
        state.users = users
    },
    set_online_user (state, user_id) {
        const index = state.users.findIndex(u => u.id == user_id)
        if (index > -1) state.users[index].online = true
    },
    set_offline_user (state, user_id) {
        const index = state.users.findIndex(u => u.id == user_id)
        if (index > -1) state.users[index].online = false
    },
    add_message (state, message) {
        state.messages.push(message)
    },
    set (state, posts) {
        state.items = posts
    },
    current (state, post) {
        state.current = post
    }
}
const actions = {
    send ({ commit }, data) {
        
    },
    addMessage ({ commit }, data) {

    },
    addChat ({ commit }, data) {
         
    }
}

const getters = {
    chatRead: state => user_id => {
        const result = state.messages.filter(m => {
            return !m.read_at && m.recipient == user_id
        }).length
        return result === 0
    },
    unreadGroupMessagesCount: state => {
        const result = []
        state.users.forEach(user => {
            result[user.id] = state.messages.filter(message => {
                return message.group && user.id != LS.get('user').id && (message.sender == user.id) && !message.read_at
            }).length
        })
        return result
    },
    unreadMessagesCount: state => {
        const result = []
        state.users.forEach(user => {
            result[user.id] = state.messages.filter(message => {
                return !message.group && user.id != LS.get('user').id && (message.sender == user.id) && !message.read_at
            }).length
        })
        return result
    },
    unreadMessages: state => state.messages.filter(m => !m.read_at),
    all: state => state.items,
    allUsers: state => {
        const users = [...state.users]
        users.sort((a, b) => {
            if (a.online && !b.online) return -1
            if (!a.online && b.online) return 1
            else {
                if (a.last_name > b.last_name) return 1
                if (a.last_name < b.last_name) return -1
                return 0
            }
        })
        return users
    },
    onlineUsers: state => state.users.filter(u => u.online),
    offlineUsers: state => state.users.filter(u => !u.online),
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}