<template>
    <table class="table table-bordered w-50 table-sm mb-3"> 
        <tbody> 
            <tr> 
                <td>Nome</td> 
                <td>{{first_name}}</td> 
            </tr>
            <tr> 
                <td>Cognome</td> 
                <td>{{last_name}}</td> 
            </tr>
            <tr> 
                <td>E-Mail</td> 
                <td>{{email}}</td> 
            </tr>
            <tr> 
                <td>Codice Fiscale</td> 
                <td>{{fiscal_code}}</td> 
            </tr>
            <tr> 
                <td>Telefono</td> 
                <td>{{telephone}}</td> 
            </tr>
            <tr> 
                <td>Corso</td> 
                <td>{{codeAndCourse}}</td> 
            </tr>
            <tr> 
                <td>Tipo di Immatricolazione</td> 
                <td>{{enrol.type}}</td> 
            </tr>
            <tr> 
                <td>Classform</td> 
                <td>{{classform}}</td> 
            </tr>
            <tr> 
                <td>ECP</td> 
                <td>{{enrol.ecp}}</td> 
            </tr>
            <tr> 
                <td>Convenzione</td> 
                <td>{{enrol.convention}}</td> 
            </tr>
            <tr> 
                <td>Programma</td> 
                <td>{{enrol.program}}</td> 
            </tr>
            <tr> 
                <td>Importo</td> 
                <td>&euro; {{enrol.import}}</td> 
            </tr>
        </tbody>
    </table>
</template>

<script>
    export default {
        props: {
            first_name: {
                type: String,
                required: true
            },
            last_name: {
                type: String,
                required: true
            },
            email: {
                type: String,
                required: true
            },
            fiscal_code: {
                type: String,
                required: true
            },
            telephone: {
                type: String,
                required: true
            },
            course: {
                type: String,
                required: true
            },
            enrol: {
                type: Object,
                required: true
            }
        },
        computed: {
            codeAndCourse() {
                return this.enrol.code + ' - ' + this.course;
            },
            classform() {
                return this.enrol.classform == 0 ? 'No' : 'Si'
            }
        }
    }
</script>