<template>
    <div class="no-print p-4">

        <span class="d-block text-uppercase font-weight-bold"><i class="fas fa-inbox mr-4"></i>Inbox</span>
        <ul class="mb-4">
            <router-link to="/inbox" tag="li" v-if="orientamento || admin">In Arrivo</router-link>
            <router-link to="/assigned" tag="li" v-if="admin">In Lavorazione</router-link>
            <router-link to="/support/inbox" tag="li" v-if="valutazione">In Arrivo</router-link>
            <router-link to="/towork" tag="li" v-if="orientamento || admin">Da Lavorare</router-link>
            <router-link to="/worked" tag="li" v-if="admin">Lavorate</router-link>
            <router-link to="/enrol" tag="li" v-if="admin">Iscrizioni Online</router-link>
            <router-link to="/try" tag="li" v-if="admin">Prova la Piattaforma</router-link>
        </ul>

        <div v-if="orientamento || valutazione">
            <span class="d-block text-uppercase font-weight-bold"><i class="far fa-envelope mr-4"></i>Le Mie Richieste</span>
            <ul class="mb-4">
                <router-link to="/user/assigned" tag="li" v-if="orientamento">In lavorazione</router-link>
                <router-link to="/support/user/inbox" tag="li" v-if="valutazione">In Lavorazione</router-link>
                <router-link to="/user/replied" tag="li" v-if="orientamento">Risposte</router-link>
                <router-link to="/user/worked" tag="li" v-if="orientamento">Lavorate</router-link>
                <router-link to="/support/user/worked" tag="li" v-if="valutazione">Lavorate</router-link>
            </ul>
        </div>

        <div v-if="orientamento">
            
            <span class="d-block text-uppercase font-weight-bold"><i class="fa fa-phone mr-4"></i>Le Mie Telefonate</span>
            <ul>
                <!-- <router-link to="/calls/assigned" tag="li">Da Lavorare</router-link> -->
                <router-link to="/user/calls/in" tag="li">In Entrata</router-link>
                <router-link to="/user/calls/out" tag="li">In Uscita</router-link>
            </ul>
            
            <span class="d-block text-uppercase font-weight-bold"><i class="fas fa-map-marker-alt mr-4"></i>Incontri in Sede</span>
            <ul>
                <router-link to="/user/meeting" tag="li">In miei Incontri</router-link>
            </ul>

        </div>

        <div v-if="admin">

            <span class="d-block text-uppercase font-weight-bold"><i class="fa fa-phone mr-4"></i>Telefonate</span>
            <ul>
                <router-link to="/calls/in" tag="li">In Entrata</router-link>
                <router-link to="/calls/out" tag="li">In Uscita</router-link>
            </ul>

            <span class="d-block text-uppercase font-weight-bold"><i class="fas fa-inbox mr-4"></i>Contatti</span>
            <ul>
                <router-link to="/contacts" tag="li">Contatti</router-link>
                <router-link to="/recall" tag="li">Recall</router-link>
            </ul>

            <span class="d-block text-uppercase"><i class="far fa-envelope mr-4"></i>Gestione</span>
            <ul>
                <router-link to="/users" tag="li">Utenti</router-link>
            </ul>

        </div>
        <div v-else>
            <span class="d-block text-uppercase font-weight-bold"><i class="fas fa-inbox mr-4"></i>Contatti</span>
            <ul>
                <router-link to="/recall" tag="li">Recall</router-link>
            </ul>
        </div>

        <div v-if="centralino">
            
            <span class="d-block text-uppercase font-weight-bold"><i class="fa fa-phone mr-4"></i>Contatore Telefonate in Entrata</span>
            <ul>
                <li>Utente 1</li>
                <li>Utente 2</li>
                <li>Utente 3</li>
            </ul>

        </div>
        
    </div>
</template>

<script>
    import LS from '../../services/localstorage'

    export default {
        name: 'sidebar',
        data() {
            return {
                user_id: LS.get('user').id
            }
        },
        computed: {
            username () {
                return LS.get('user').first_name + ' ' + LS.get('user').last_name;
            },
            picture () {
                return 'https://crm.unimercatorum.it/uploads/users/'+LS.get('user').photo
            },
            group () {
                let group = LS.get('user').group;
                switch (group) {
                    case 1:
                        return 'Orientamento'
                        break;
                    case 2:
                        return 'Valutazione CFU'
                        break;
                    case 6:
                        return 'Amministratore'
                        break;
                    case 7:
                        return 'Centralino'
                        break;
                }
            },
            admin () {
                return LS.get('user').group === 6
            },
            orientamento () {
                return LS.get('user').group === 1
            },
            valutazione () {
                return LS.get('user').group === 2
            },
            centralino () {
                return LS.get('user').group === 7
            }
        }
    }
</script>

<style scoped>
    * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }

    span {
        color: #c1cddb;
    }

    ul li {
        color:#fff;
        margin:10px 0;
        cursor: pointer;
        list-style:none;
    }
</style>