<template>
    <component :href="href" :is="tag" @click="$emit('click')" :class="buttonClass" :disabled="disabled">
        <i v-if="loading" class="fas fa-circle-notch fa-spin mr-2"></i>
        <i v-else :class="iconClass"></i><slot></slot>
    </component>
</template>
<script>
    export default {
        name: 'ui-button',
        props: {
            classes: {
                type: String,
                required: false,
                default: ''
            },
            tag: {
                type: String,
                required: false,
                default: 'button'
            },
            disabled: {
                type: Boolean,
                required: false,
                default: false
            },
            icon: {
                type: String,
                required: false,
                default: 'fas fa-info'
            },
            loading: {
                type: Boolean,
                required: false,
                default: false
            },
            size: {
                default: '',
                validator: value => {
                    return ['sm', 'lg', ''].indexOf(value) > -1
                }
            },
            color: {
                default: 'primary',
                validator: value => {
                    return ['success', 'warning', 'danger', 'secondary', 'primary', 'info', 'dark', 'light', 'crm', 'outline-secondary'].indexOf(value) > -1
                }
            },
            outline: {
                type: Boolean,
                required: false,
                default: false
            },
            href: {
                type: String,
                required: false
            },
            rounded: {
                type: Boolean,
                required: false,
                default: false
            }
        },
        computed: {
            iconClass () {
                return this.icon + ' mr-2'
            },
            buttonClass () {
                let btnType = `btn-${this.color}`
                let btnSize = `btn-${this.size}`
                if (this.outline) btnType = `btn-outline-${this.color}`
                if (this.size === '') btnSize = ''
                return `btn ${btnType} ${btnSize} px-3 ${this.classes}`
            }
        }
    }
</script>
<style scoped>
    .btn-crm {
        background-color: #802144;
        color: #fff;
        border-color: #802144;
    }
    .btn-crm:hover {
        background-color: #5c112d;
        border-color: #5c112d;
        color: #fff
    }
</style>