<template>
    <div class="position-absolute p-5 d-flex flex-column position-0">
        
        <div><h4 class="font-weight-bold mb-4">{{ pageTitle }}</h4></div>

        <analytics-widgets v-if="inInbox" class="mb-3" />

        <div class="row">
            <div class="col">
                <div class="bg-white rounded-top-lg shadow-sm">
                    <div class="row p-3">
                        <div class="col-6">
                            <button type="button" class="btn btn-outline-secondary btn-sm rounded-lg px-3" :class="{
                                disabled: loading,
                                animated: newRequest,
                                flash: newRequest,
                                infinite: newRequest
                            }" @click="refresh">
                                <i :class="{'fa-spin': loading}" class="fas fa-sync-alt mr-2 pause-spinner"></i>Aggiorna
                            </button>
                            <div class="d-inline-block" v-if="type != 4 && type != 2">
                                <select class="custom-select custom-select-sm border-secondary rounded-lg" v-model="filters.type">
                                    <option value="">Tipologia (Tutte)</option>
                                    <option value="1">Informazioni</option>
                                    <option value="3">Valutazioni CFU</option>
                                </select>
                            </div>
                            <div class="d-inline-block" v-if="type != 4 && type != 2">
                                <select class="custom-select custom-select-sm border-secondary rounded-lg" v-model="filters.category">
                                    <option value="">Categoria (Tutte)</option>
                                    <option value="1">Generica</option>
                                    <option value="2">Corsi di Laurea</option>
                                    <option value="3">Master 1° Livello</option>
                                    <option value="4">Master 2° Livello</option>
                                    <option value="5">Alta Formazione</option>
                                    <option value="7">Perfezionamenti</option>
                                    <option value="11">Esami Singoli</option>
                                    <option value="12">24 CFU</option>
                                    <option value="14">Certificazioni</option>
                                </select>
                            </div>
                            <div class="d-inline-block" v-if="type != 4 && type != 2">
                                <select class="custom-select custom-select-sm border-secondary rounded-lg" v-model="filters.provider">
                                    <option value="">Provenienza (Tutte)</option>
                                    <option value="website">Sito Web</option>
                                    <option value="landing">Pubblicità</option>
                                    <option value="AMP">AMP</option>
                                    <option value="messenger">Messenger</option>
                                </select>
                            </div>
                        </div>
                        <pager
                            :loading="loading"
                            :total="total"
                            :start="start"
                            :end="end"
                            :prev="prevDisabled"
                            :next="nextDisabled"
                            :pages="pages"
                            :page="page"
                            @prev="goPrev"
                            @next="goNext"
                            @change="p => go(p)"
                        />
                    </div>
                    <div class="bg-gray border-top border-bottom p-3 text-uppercase small">
                        <div class="row">
                            <div class="col-3">Nome</div>
                            <div class="col-4">Richiesta</div>
                            <div class="col-1">Da</div>
                            <div class="col-3">Operatore</div>
                            <div class="col-1">Data</div>
                        </div>
                    </div>                    
                </div>
            </div>
        </div>

        <div class="flex-grow-1">
            <div class="position-relative h-100 w-100 overflow-x-hidden overflow-y-auto rounded-bottom-lg">
                <horizontal-slide :dir="direction">
                    <ul class="list-group position-absolute w-100 shadow-sm" :key="page">
                        <li v-if="loading" key="loading" class="rounded-0 list-group-item pointer bg-light text-center">
                            <span v-if="loading">
                                <i class="fas fa-circle-notch fa-spin"></i>
                            </span>
                        </li>
                        <li v-else-if="requests.length < 1" key="no-results" class="rounded-0 list-group-item pointer bg-light text-center">
                            <div>Nessuna richiesta presente</div>
                        </li>
                        <request-item v-for="(request, index) in requests" v-else-if="requests.length > 0" 
                            :support_pending="request.support_pending > 0"
                            :disabled="request.busy"
                            :key="index"
                            :id="request.id"
                            :contact="{
                                id: request.cid,
                                first_name: request.first_name,
                                last_name: request.last_name
                            }"
                            :user="{
                                id: request.user_id,
                                first_name: request.user_first_name,
                                last_name: request.user_last_name    
                            }"
                            :first_name="request.first_name"
                            :last_name="request.last_name"
                            :type="{
                                id: request.type,
                                class: request.type_class,
                                icon_color: request.type_color_icon,
                                name: request.type_name
                            }"
                            :subject="request.subject"
                            :status="{
                                id: request.status,
                                class: request.status_class,
                                icon: request.status_icon,
                                name: request.status_name
                            }"
                            :category="{
                                id: request.category,
                                name: request.category_name
                            }"
                            :replies_count="request.replies_count"
                            :updated_at="request.updated_at"
                            :provider="request.provider"
                            :campaign="request.campaign"
                        ></request-item>
                    </ul>
                </horizontal-slide>
            </div>
        </div> 

    </div>
</template>

<script>
    import { mapGetters, mapActions, mapState } from 'vuex'
    import services from '../services'
    import RequestItem from './inbox/RequestItem'
    import Toolbar from './Toolbar'
    import HorizontalSlide from './transitions/HorizontalSlide'
    import AnalyticsWidgets from './analytics/AnalyticsWidgets'
    
    
    export default {
        name: 'inbox',
        components: {RequestItem, AnalyticsWidgets, HorizontalSlide, Toolbar},
        props: {
            type: {
                type: Number | Boolean,
                required: false,
                default: false
            },
            status: {
                type: Number | Boolean,
                required: false,
                default: false
            },
            category: {
                type: Number | Boolean,
                required: false,
                default: false
            },
            user: {
                type: Number | Boolean,
                required: false,
                default: false
            },  
            provider: {
                type: String,
                required: false,
                default: null
            },
            page: {
                type: Number | String,
                required: false,
                default: 1
            }
        },
        data () {
            return {
                direction: 1,
                newRequest: false,
                filters: {
                    category: '',
                    provider: '',
                    type: ''
                }
            }
        },
        computed: {
            prevDisabled () {
                return !this.prev || this.loading
            },
            nextDisabled () {
                return !this.next || this.loading
            },
            prevPageLink () {
                if (!this.prev) return this.$route.path
                return this.basePath + '/' + this.prev
            },
            nextPageLink () {
                if (!this.next) return this.$route.path
                return this.basePath + '/' + this.next
            },
            basePath () {
                if (this.$route.params && this.$route.params.page) {
                    const index = this.$route.path.lastIndexOf('/')
                    return this.$route.path.substring(0, index)
                }
                return this.$route.path
            },
            next () {
                const next = parseInt(this.page) + 1
                if (next > this.pages) return undefined
                return next
            },
            prev () {
                const prev = parseInt(this.page) - 1
                if (prev === 0) return undefined
                return prev
            },
            params () {
                return {
                    status: this.status,
                    category: this.filters.category || this.category,
                    user: this.user,
                    type: this.filters.type || this.type,
                    provider: this.filters.provider || this.provider,
                    page: this.page
                }
            },
            inInbox () {
                return /\/inbox(\/\d+)?/i.test(this.$route.path)
            },
            ...mapState('request', {
                requests: state=> state.requests.items,
                start: state => state.requests.start + 1,
                end: state => state.requests.end,
                current_page: state => state.requests.page,
                pages: state => state.requests.pages,
                total: state => state.requests.total,
                quantity: state=> state.requests.qty,
                loading: 'loading'
            }),
            ...mapState('page', {
                pageTitle: 'title'
            })
        },
        methods: {
            ...mapActions('request', {
                getRequests: 'getAll'
            }),
            goNext() {
                this.$router.push({params: {page: parseInt(this.page) + 1}})
            },
            goPrev() {
                this.$router.push({params: {page: parseInt(this.page) - 1}})
            },
            go (p) {
                this.$router.push({params: {page: parseInt(p)}})
            },
            refresh () {
                this.newRequest = false
                if (this.loading) return
                this.load()
            },
            load () {
                this.getRequests(this.params)
            }
        },
        mounted () {
            this.load()
            services.ws.on('REQUEST_NEW', () => {
                this.newRequest = true
            })
        },
        beforeDestroy () {
            services.ws.off('REQUEST_NEW')
        },
        watch: {
            params (new_value) {
                this.load()
            },
            page (new_value, prev_value) {
                const diff = prev_value - new_value
                this.direction = diff < 0 ? 1 : 0
                this.load()
            }
        }
        
    }
</script>

<style scoped>
    * {
        -webkit-touch-callout: none;
        -webkit-user-select: none;
        -khtml-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
</style>