<template>
    <page>
        <div class="row h-100 justify-content-center align-items-center">
            <div class="col-3">
                <h2 class="mb-5 text-center"><i class="far fa-compass mr-3 fa-lg"></i><strong>CRM</strong> Multiversity</h2>
                <div class="bg-white shadow-sm p-5 rounded-lg">
                    <h3 class="mb-4">Login</h3>
                    <div>
                        <input type="hidden" name="remember" value="0">
                        <div class="form-group">
                            <label>E-Mail</label>
                            <input v-model="email" @keydown="keydown" type="email" class="form-control form-control-lg mb-2 rounded-lg border-2" required>
                        </div>
                        <div class="form-group">
                            <label>Password</label>
                            <input v-model="password" @keydown="keydown" type="password" class="form-control form-control-lg mb-2 rounded-lg border-2" required>
                        </div>
                        <div class="form-group">
                            <label>Università</label>
                            <select v-model="conn" class="custom-select border-2 rounded-lg">
                                <!-- <option value="pegaso">Pegaso</option> -->
                                <option value="local">Mercatorum</option>
                            </select>
                        </div>
                        <button @click="() => signin({email, password, conn})" type="submit" class="btn btn-dark btn-lg rounded-lg mt-4">
                            <strong v-if="!loading">Accedi</strong>
                            <div v-else>
                                <div class="spinner-border" role="status">
                                    <span class="sr-only">Loading...</span>
                                </div>
                            </div>
                        </button>
                    </div>
                    <div v-if="errors.length">
                        Credenziali non valide
                    </div>
                </div>
            </div> 
        </div> 
    </page>
</template>

<script>

    import { mapGetters, mapActions, mapState } from 'vuex'
    import Page from './layout/Page';
    export default {
        name: 'login',
        components: {Page},
        data () {
            return {
                email: '',
                password: '',
                conn: 'pegaso',
                remember: false
            }
        },
        methods: {
            ...mapActions('auth', {
                signin: 'signin'
            }),
            keydown (e) {
                if (e.which === 13) this.signin({
                    email: this.email,
                    password: this.password,
                    conn: this.conn
                })
            }
        },
        computed: {
            ...mapState('auth', {
                user: 'user',
                errors: 'errors'
            }),
            ...mapState('auth', {
                loading: 'logging'
            })
        }
    }
</script>

<style scoped>
    body, html, .bg-gray {
        background-color:#eaeaea!important;
    }
    
    .border-2 {
        border-width:2px!important;
    }

    .form-control:focus{
        box-shadow:none!important;
    }

    .custom-select {
        height: calc(1.5em + 1rem + 2px);
        padding: .5rem 1rem;
        font-size: 1.25rem;
        line-height: 1.5;
    }
</style>
