import services from '../../services'
import router from '../../router'

const state = {
    user: {},
    logging: false,
    logged: false,
    errors: []
}

const mutations = {
    loggingIn (state) {
        state.logging = true
    }, 
    error (state, payload) {
        state.errors = payload
        state.logging = false
    },
    loggedIn (state, user) {
        state.logged = true
        state.logging = false
        state.user = user
    },
    loggedOut (state) {
        state.logged = false
        state.user = null
        state.logging = false
    }
}

const actions = {

    getUser ({ commit }) {
        services.getUser().then(user => {
            commit('loggedIn', user)
        }).catch(error => {
            console.log(error)
        })
    },

    signin ({ commit }, {email, password, conn}) {
        commit('loggingIn')
        services.signin(email, password, conn).then(user => {
            commit('loggedIn', user)
            router.push({ name: 'Home' });
        }).catch(error => {
            commit('error', error)
        })
    }
    
}

const getters = {
    user: state => state.user,
    group: state => {
        switch (state.user.group) {
            case 1:
                return 'Orientamento'
                break
            case 2:
                return 'Valutazione CFU'
                break
            case 6:
                return 'Amministratore'
                break
            case 7:
                return 'Centralino'
                break
        }
        return 'fuck'
    },
    errors: state => state.errors
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}