<template>
    <div style="display:inline-block">
        <ui-button icon="fa fa-phone" color="warning" @click="modal = true" class="btn-inline" size="sm">CONFERMA INTERVENTO</ui-button>
        <modal :open="modal" @close="modal = false" size="lg" title="Conferma Intervento">
            <template v-slot:footer>
                <ui-button icon="fa fa-times" @click="cancel" color="danger">Annulla</ui-button>
                <ui-button icon="fa fa-phone" color="success" :loading="loading" @click="submit">Conferma Intervento</ui-button>
            </template>
            <form ref="form" v-if="reply">
                <div class="form-group row">
                    <div class="col-sm-12">
                        <editor :disabled="loading" v-model="content"></editor>
                    </div>
                </div>
                <div class="form-group row">
                    <div class="col-sm-2"></div>
                    <div class="col-sm-10">
                        <ul class="p-0">
                            <li class="text-danger" v-for="error in errors">{{ error.join(',') }}</li>
                        </ul>
                    </div>
                </div>
            </form>
        </modal>
    </div>
</template>
<script>
    import services from '../../services'
    import LS from '../../services/localstorage'
    import Modal from '../layout/Modal'

    export default {
        components: { Modal },
        name: 'confirm-support',
        computed: {
            selectableUsers () {
                return this.users.filter(u => u.id != LS.get('user').id)
            }
        },
        mounted () {
            services.getReply(this.reply_id).then(response => {
                this.reply = response
                this.content = response.body
            }).catch(error => {
                alert(error)
            }).then(() => {
            })
        },
        props: {
            reply_id: {
                type: String | Number,
                reqired: true
            }
        },
        data () {
            return {
                errors: [],
                loading: false,
                modal: false,
                reply: false,
                content: ''
            }
        },
        methods: {
            cancel () {
                this.reset()
                this.modal = false
            },
            submit () {
                if (this.user === '') {
                    return this.showErrors({to_group_id: ['Utente non selezionato']})
                }
                this.loading = true
                services.confirmReplyFromSupport(this.reply.id, {content: this.content}).then(response => {
                    this.loading = false
                    if (response) {
                        this.reset()
                        this.modal = false
                        this.$emit('sent')
                    }
                }).catch(error => {
                    this.loading = false
                    this.showErrors(error)
                })
            },
            showErrors (error) {
                this.errors = error
                setTimeout(() => {
                    this.errors = []
                }, 4000)
            },
            reset () {
                this.errors = []
                this.note = ''
            }
        }
    }
</script>