<template>
    <div class="position-absolute position-0 d-flex flex-column">
        
        
        <main-header @menu="fixed = !fixed; open = !open" :menu="open" />
        <div class="d-flex flex-row position-relative h-100">
            <div @mouseenter="openMenu()" @mouseleave="closeMenu()">
                <aside class="left h-100">
                    <sidebar />
                </aside>
            </div>
            <div class="d-flex flex-column flex-grow-1 position-relative">
                <div class="position-relative h-100 position-0 overflow-y-auto">
                    <router-view :key="routeKey"></router-view>
                </div>
            </div>
            <div>
                <aside class="right h-100 border-left">
                    <div class="p-4">
                        <chat />
                    </div>
                </aside>
            </div>
        </div>
    </div>
</template>

<script>
    import Page from './layout/Page'
    import MainHeader from './layout/MainHeader'
    import Sidebar from './layout/Sidebar'
    import FadeInDownOutUp from './transitions/FadeInDownOutUp'
    import Chat from './Chat'
    import services from '../services'

    import { mapGetters, mapActions, mapState } from 'vuex'

    export default {
        name: 'home',
        components: {Page, Sidebar, MainHeader, FadeInDownOutUp, Chat},
        methods: {
            ...mapActions('auth', {
                getUser: 'getUser'
            }),
            openMenu: function () {
                if (this.open) return false
                this.$options.openTimeout = setTimeout(() => {
                    this.closed = false
                }, this.$options.openDelay)
            },
            closeMenu: function () {
                clearTimeout(this.$options.openTimeout)
                if (this.open) return false
                this.closed = true
            }
        },
        data () {
            return {
                open: true,
                closed: true,
                fixed: false
            }
        },
        openDelay: 400,
        openTimeout: null,
        computed: {
            sidebarClass () {
                return {
                    sidebar: true,
                    closed: this.closed && !this.open,
                    fixed: this.fixed,
                    flex: !this.fixed
                }
            },
            mainClass () {
                return {
                    'd-flex': true,
                    'flex-column': true,
                    'flex-grow-1': true,
                    'position-relative': true,
                    margin: this.fixed
                }
            },
            routeKey () {
                return this.basePath
            },
            basePath () {
                if (this.$route.params && this.$route.params.page) {
                    const index = this.$route.path.lastIndexOf('/')
                    return this.$route.path.substring(0, index)
                }
                return this.$route.path
            }
        },
        mounted () {
            this.getUser()
        }
    }
</script>

<style scoped>
    aside.left {
        background-image:linear-gradient(0deg,#7e8ea1,#3c4655);
        width:300px;
    }
    aside.right {
        background-color:#eef1f4;
        width:300px;
    }
</style>