<template>
    <component :is="tag">{{ formatted }}</component>
</template>
<script>
    import moment from 'moment'

    export default {
        name: 'date-time',
        props: {
            at: {
                type: String | Number,
                required: true
            },
            format: {
                type: String,
                required: false,
                default: 'D MMM H:mm'
            },
            tag: {
                type: String,
                required: false,
                default: 'span'
            }
        },
        computed: {
            today () {
                const today = moment()
                const datetime = moment(this.at)
                return datetime.isSame(today, 'day');
            },
            formatted () {
                return this.today ? `Oggi ${moment(this.at).format('H:mm')}` : moment(this.at).format(this.format)
            }
        }
    }
</script>