<template>
    <li class="rounded-0 pointer bg-white border-bottom px-3 py-2" @click="$emit('click', meeting.note)">
        <div class="row align-items-center">
            <div class="col-3 text-capitalize" v-if="showName">
                <router-link :to="`/contact/${meeting.contact_id}`">{{ `${meeting.contact_first_name} ${meeting.contact_last_name}` }}</router-link>
            </div>
            <div :class="{
                    'col-5': showName,
                    'col-7': !showName,
                    'text-truncate': true
                }">
                <span class="btn btn-xs btn-outline-secondary mr-1 rounded-lg" v-if="meeting.category_name">{{ meeting.category_name }}</span>
                <span class="text-muted">{{ (meeting.subject) ? meeting.subject : 'Generica' }}</span>
            </div>
            <div class="col-1 small text-truncate">
                <button 
                    class="btn btn-xs btn-block btn-outline-secondary mr-1 rounded-lg" 
                    @click="$emit('note', meeting.note)"
                    v-if="meeting.note != ''">
                    <i class="far fa-file-alt mr-2"></i>Note
                </button>
            </div>
            <div class="col-2 text-left small">
                <i class="far fa-user-circle mr-2"></i>{{ userName }}
            </div>
            <div class="col-1 text-right text-muted small">
                <date-time :at="meeting.created_at" />
            </div>
        </div>
    </li>
</template>

<script>
    export default {
        props: {
            meeting: {
                type: Object,
                required: true
            },
            showName: {
                type: Boolean,
                required: false,
                default: true
            }
        },
        computed: {
            userName () {
                return this.meeting.user_first_name.substring(0, 1) + '. ' + this.meeting.user_last_name
            }
        }
    }
</script>