<template>
    <div class="position-absolute p-5 d-flex flex-column position-0">

        <div class="d-flex">
            <div><h4 class="font-weight-bold mb-4">{{ directionTitle }}</h4></div>
            <div class="ml-auto">
                <incoming-calls v-if="call_direction == 'in'"/>
            </div> 
        </div>
        <div class="row">
            <div class="col">
                <div class="bg-white rounded-top-lg shadow-sm">
                    <div class="row p-3">
                        <div class="col-6">
                            <ui-button
                                @click="$router.go(-1)" 
                                color="outline-secondary"
                                size="sm"
                                icon="fas fa-chevron-left"
                                classes="rounded-lg">
                                Torna Indietro
                            </ui-button>
                            <button type="button" class="btn btn-outline-secondary btn-sm rounded-lg px-3" :class="{disabled: loading}" @click="refresh"><i :class="{'fa-spin': loading}" class="fas fa-sync-alt mr-2 pause-spinner"></i>Aggiorna</button>
                        </div>
                        <pager
                            :loading="loading"
                            :total="total"
                            :start="start"
                            :end="end"
                            :prev="prevDisabled"
                            :next="nextDisabled"
                            :pages="pages"
                            :page="page"
                            @prev="goPrev"
                            @next="goNext"
                            @change="p => go(p)"
                        />
                    </div>
                    <div class="bg-gray border-top border-bottom p-3">
                        <div class="row">
                            <div class="col-3">Nome</div>
                            <div class="col-4">Richiesta</div>
                            <div class="col-1">Note</div>
                            <div class="col-3">
                                <div class="d-flex">
                                    <div class="w-50">Operatore</div>
                                    <div class="w-50 text-center">Responso</div>
                                </div>
                            </div>
                            <div class="col-1 text-right">Data</div>
                        </div>
                    </div>                    
                </div>
            </div>
        </div>
        <div div style="flex-grow: 1">
            <div class="position-relative slide-wrapper h-100 w-100" style="overflow-x: hidden">
                <horizontal-slide :dir="direction">
                    <ul class="list-group position-absolute left-0 right-0" :key="page">
                        <li v-if="loading" key="loading" class="rounded-0 list-group-item pointer bg-light text-center">
                            <span v-if="loading">
                                <i class="fas fa-circle-notch fa-spin"></i>
                            </span>
                        </li>
                        <call-item
                            v-else-if="calls.length > 0"
                            @note="note => modal = note"
                            v-for="(call, index) in calls"
                            :key="index"
                            :call="call"
                        />
                        <li v-else class="rounded-0 list-group-item pointer bg-light text-center">Nessuna telefonata presente</li>
                    </ul>
                </horizontal-slide>
            </div>
        </div>
        <modal :open="modal ? true : false" @close="modal = false" title="Note Telefonata">
            <template v-slot:footer>
                <button data-v-3dea25d8="" type="button" data-dismiss="modal" class="btn btn-secondary" @click="modal = false">Chiudi</button>
            </template>
            <div v-html="modal"></div>
        </modal>
    </div>
</template>

<script>
    import LS from '../services/localstorage'
    import services from '../services'
    import Toolbar from './Toolbar'
    import HorizontalSlide from './transitions/HorizontalSlide'
    import CallItem from './call/CallItem'
    import Modal from './layout/Modal'
    import IncomingCalls from './modals/IncomingCalls'
    import OutgoingCalls from './modals/OutgoingCalls'
    import UIButton from './layout/UIButton'

    export default {
        name: 'calls',
        components: { Toolbar, HorizontalSlide, CallItem, Modal, IncomingCalls, OutgoingCalls, UIButton },
        props: {
            call_direction: {
                type: String,
                required: false
            },
            page: {
                type: Number | String,
                required: false,
                default: 1
            }
        },
        mounted () {
            this.load()
        },
        data () {
            return {
                modal: false,
                start: 0,
                end: 0,
                quantity: 0,
                current_page: 1,
                pages: 0,
                total: 0,
                loading: false,
                calls: [],
                direction: 1
            }
        },
        directions: [
            'in',
            'out'
        ],
        methods: {
            goNext() {
                this.$router.push({params: {page: parseInt(this.page) + 1}})
            },
            goPrev() {
                this.$router.push({params: {page: parseInt(this.page) - 1}})
            },
            go (p) {
                this.$router.push({params: {page: parseInt(p)}})
            },
            refresh () {
                if (this.loading) return
                this.load()
            },
            load () {
                this.loading = true 
                let params = {
                    direction: this.directionId, 
                    page: this.page, 
                    user_id: this.own ? LS.get('user').id : false
                }
                services.getCalls(params).then(data => {
                    this.calls = data.items
                    this.start = data.start + 1
                    this.end = data.end
                    this.current_page = data.page
                    this.pages = data.pages
                    this.total = data.total
                    this.quantity = data.qty
                    this.loading = false
                }).catch(error => {
                    this.loading = false
                })
            }
        },
        watch: {
            page (new_value, prev_value) {
                const diff = prev_value - new_value
                this.direction = diff < 0 ? 1 : 0
                this.load()
            }
        },
        computed: {
            directionId () {
                return this.$options.directions.indexOf(this.call_direction) + 1
            },
            prevDisabled () {
                return !this.prev || this.loading
            },
            nextDisabled () {
                return !this.next || this.loading
            },
            prevPageLink () {
                if (!this.prev) return this.$route.path
                return this.basePath + '/' + this.prev
            },
            nextPageLink () {
                if (!this.next) return this.$route.path
                return this.basePath + '/' + this.next
            },
            basePath () {
                if (this.$route.params && this.$route.params.page) {
                    const index = this.$route.path.lastIndexOf('/')
                    return this.$route.path.substring(0, index)
                }
                return this.$route.path
            },
            next () {
                const next = parseInt(this.page) + 1
                if (next > this.pages) return undefined
                return next
            },
            prev () {
                const prev = parseInt(this.page) - 1
                if (prev === 0) return undefined
                return prev
            },
            directionTitle() {
                if (this.call_direction == 'in') {
                    return 'Telefonate in Entrata'
                } else if (this.call_direction == 'out') {
                    return 'Telefonate in Uscita'
                } else {
                    return ''
                }
            }
        }
    }
</script>
<style scoped>
    .left-0 {
        left: 0;
    }
    .right-0 {
        right: 0;
    }
</style>