<template>
    <transition :enter-active-class="`animated ${enter} faster`" :leave-active-class="`animated ${leave} faster`">
        <slot></slot>
    </transition>
</template>
<script>
    export default {
        name: 'horizontal-slide',
        props: {
            dir: {
                //0 backwards, 1 forward
                type: Number,
                required: false,
                default: 0
            }
        },
        computed: {
            enter () {
                return this.dir ? 'fadeInRight' : 'fadeInLeft'
            },
            leave () {
                return this.dir ? 'fadeOutLeft' : 'fadeOutRight'
            }
        }
    }
</script>