<template>
    <div :class="classes">
        <div class="chat-header d-flex p-2 text-secondary bg-white" @click="$emit('toggle', user.id)">
            <div class="chat-title d-flex align-items-center flex-grow-1">
                <small><i :class="`online fas fa-circle font-weight-bold mr-2 small ${user.online ? 'text-success' : 'text-secondary'}`"></i></small>
                <div class="d-flex align-items-center justify-content-between flex-grow-1">
                    <div class="flex-grow">{{ user.first_name }} {{ user.last_name }}</div>
                    <div class="ml-2 new badge badge-danger animated flash" v-if="messages_count > 0">{{ messages_count }}</div>
                </div>
            </div>
            <div class="chat-buttons d-flex px-2">
                <div title="Riduci a icona" class="chat-minimize mr-3"><i :class="minimizeIconClass"></i></div>
                <div title="Chiudi la conversazione" class="chat-close"><i :class="closeIconClass" @click.stop="$emit('close')"></i></div>
            </div>
        </div>
        <div class="chat-body" @click="$refs.textarea.focus()">
            <div class="message-box" ref="box">
                <div class="text-center text-muted cursor-pointer" @click="moreHistory" v-if="more_history">carica messaggi precedenti</div>
                <div class="history-box"></div>
                <chat-message v-for="(message, index) in messages" :message="message" :key="index" />
            </div>
            <div class="text-area p">
                <div class="text-muted small font-italic typing p-2 d-block d-flex bg-white justify-content-between" style="height:25px">
                    <div><span v-if="typing"><i class="fas fa-pencil-alt mr-1"></i>Sta scrivendo...</span></div>
                    <div><span v-if="read"><i class="fas fa-check-double mr-1"></i>Visualizzato</span></div>
                </div>
                <textarea @focus="focus" @blur="blur" ref="textarea" placeholder="Scrivi qui il messaggio" class="chat-input p-3" v-model="input" @keydown="keydown"></textarea>
            </div>
        </div>
    </div>
</template>
<script>
    import { mapState } from 'vuex'
    import services from '../../services'
    import LS from '../../services/localstorage'
    import ChatMessage from './ChatMessage'
    import store from '../../store'

    export default {
        name: 'conversation',
        components: { ChatMessage },
        data () {
            return {
                input: '',
                focused: false
            }
        },
        props: {
            user: {
                type: Object,
                required: true
            },
            messages: {
                type: Array,
                required: false,
                default: () => []
            },
            more_history: {
                type: Boolean,
                required: false,
                default: true
            },
            minimized: {
                type: Boolean,
                required: false,
                default: false
            },
            typing: {
                type: Boolean,
                required: false,
                default: false
            },
            read: {
                type: Boolean,
                required: true
            },
            messages_count: {
                type: Number,
                required: false,
                default: 0
            },
            group: {
                type: Boolean,
                required: false,
                default: false
            }
        },
        methods: {
            focus () {
                this.focused = true
                store.commit('chat/set_messages_as_read', {id: this.user.id, read_at: (new Date).toISOString()})
                services.chat.send('CHAT_READ', this.user.id)
            },
            blur () {
                this.focused = false
            },
            keydown (e) {
                //this.focus()
                if (e.which === 13) {
                    e.preventDefault()
                    this.send()
                    return false
                } else {
                    services.chat.send('TYPING', this.user.id)
                }
            },
            send () {
                const text = this.input.trim()
                const event = this.group ? 'CHAT_GROUP_MESSAGE' : 'CHAT_MESSAGE'
                services.chat.send(event, {
                    photo: LS.get('user').photo,
                    recipient: this.user.id,
                    text
                })
                this.input = ''
            },
            loadHistory (last_id = null) {
                const payload = {
                    sender: this.user.id
                }
                if (last_id) payload.last_id = last_id
                services.chat.send('HISTORY', payload)
            },
            moreHistory () {
                if (this.messages.length) {
                    this.loadHistory(this.messages[0].id)
                }
                this.loadHistory()
            }
        },
        computed: {
            classes () {
                return {
                    minimized: this.minimized,
                    chat: true,
                    'chat mr-2': true
                }
            },
            minimizeIconClass () {
                return {
                    'far fa-window-maximize': this.minimized,
                    'fas fa-minus': !this.minimized,
                    'color-focused': this.focused,
                    'color-blurred': !this.focused
                }
            },
            closeIconClass () {
                return {
                    'fas': true,
                    'fa-times': true,
                    'color-focused': this.focused,
                    'color-blurred': !this.focused
                }
            }
        },
        typingTimeout: null,
        watch: {
            typing (n, o) {
                if (n) {
                    clearTimeout(this.$options.typingTimeout)
                    this.$options.typingTimeout = setTimeout(() => {
                        store.commit('chat/unset_user_typing', this.user.id)
                    }, 3000)
                }
            },
            messages: {
                handler (n, o) {
                    if (n.length < 1) return
                    if (this.focused) {
                        this.$nextTick().then(() => {
                            services.chat.send('CHAT_READ', this.user.id)
                            store.commit('chat/set_messages_as_read', {id: this.user.id, read_at: (new Date).toISOString()})
                        })
                    }
                    if (o.length < 1) {
                        this.$nextTick().then(() => {
                            this.$refs.box.scrollTop = this.$refs.box.scrollHeight
                        })
                        return
                    }
                    if (o[o.length - 1].id < n[n.length - 1].id) {
                        this.$nextTick().then(() => {
                            this.$refs.box.scrollTop = this.$refs.box.scrollHeight
                        })
                    }
                }
            },
            minimized (n, o) {
                if (!n && o) {
                    this.$nextTick().then(() => {
                        this.$refs.textarea.focus()
                    })
                }
            }
        },
        mounted () {
            this.$refs.textarea.focus()
            this.$refs.box.scrollTop = this.$refs.box.scrollHeight
            services.chat.send('CHAT_READ', this.user.id)
            this.loadHistory()
        }
    }
</script>
<style scoped>
    .chat-minimize i:hover, .chat-close i:hover {
        opacity: .5
    }
    .color-focused {
        color: #262d37;
    }
    .color-blurred {
        color: #E2E2E2;
    }

    .chat .chat-body {
        border-left:1px solid #ccc;
        border-right: 1px solid #ccc;
    }

    .chat.minimized .chat-body {
        height: 0;
        transition: all 400ms cubic-bezier(0.500, 0.250, 0.090, 1);;
    }

    .chat .chat-body {
        height: 396px;
        transition: all 400ms cubic-bezier(0.500, 0.250, 0.090, 1);;
    }

    .message-box {
        height: 300px;
        overflow: auto;
    }
    .chat {
        width: 300px;
        
        
    }
    .chat .message-box {
        background: white;
    }

    
    .chat textarea {
        resize: none;
        display:block;
        border: none;
        overflow: auto;
        outline: none;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
        border-top: 1px solid #ccc;
        width:100%;
        margin:0;
    }

    .chat {
        background:
    }

    .chat-header {
        border-width: 1px;
        border-style: solid;
        border-top-left-radius: 10px;
        border-top-right-radius: 10px;
        border-color: #ccc;
        cursor: pointer;
    }

    .chat-header.blink {
        animation:blink 2s;
        -moz-animation:blink 2s infinite; /* Firefox */
        -webkit-animation:blink 2s infinite;
    }
    .message-text-div {
        word-break: break-word;
    }

    @-moz-keyframes blink
    {
        0%   {background:#5c112d; border-color: #5c112d;}
        50%   {background:#802144; border-color:#802144;}
        100%   {background:#5c112d; border-color: #5c112d;}
    }
    
    @-webkit-keyframes blink
    {
        0%   {background:#5c112d; border-color: #5c112d;}
        50%   {background:#802144; border-color:#802144;}
        100%   {background:#5c112d; border-color: #5c112d;}
    }
</style>