<template>
    <div class="position-absolute p-5 d-flex flex-column position-0">
        
        <div><h4 class="font-weight-bold mb-4">Recall per Iscrizione Online</h4></div>
        
        <div class="row">
            <div class="col">
                <div class="bg-white rounded-top-lg shadow-sm">
                    <div class="row p-3">
                        <div class="col-6">
                            <button 
                                type="button" 
                                class="btn btn-outline-secondary btn-sm rounded-lg px-3" 
                                :class="{disabled: loading}" 
                                @click="refresh">
                                <i :class="{'fa-spin': loading}" class="fas fa-sync-alt mr-2 pause-spinner"></i>Aggiorna
                            </button>              
                        </div>
                        <pager
                            :loading="loading"
                            :total="total"
                            :start="start"
                            :end="end"
                            :prev="prevDisabled"
                            :next="nextDisabled"
                            :pages="pages"
                            :page="page"
                            @prev="goPrev"
                            @next="goNext"
                            @change="p => go(p)"
                        />
                    </div>
                    <div class="bg-gray border-top border-bottom p-3 text-uppercase small">
                        <div class="row">
                            <div class="col-6">Nome</div>
                            <div class="col-3">E-Mail</div>
                            <div class="col-2">Telefono</div>
                            <div class="col-1 text-right">Data</div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div class="flex-grow-1">
            <div class="position-relative h-100 w-100 overflow-x-hidden overflow-y-auto rounded-bottom-lg">
                <horizontal-slide :dir="direction">
                    <ul class="list-group position-absolute w-100 shadow-sm" :key="page">
                        <li v-if="loading" key="loading" class="rounded-0 list-group-item pointer bg-light text-center">
                            <span v-if="loading">
                                <i class="fas fa-circle-notch fa-spin"></i>
                            </span>
                        </li>
                        <contact-item v-else 
                            v-for="(contact, index) in contacts"
                            :key="index"
                            :contact="{id: contact.contact_id, ...contact}"
                        />
                    </ul>
                </horizontal-slide>
            </div>
        </div>
    </div>
</template>

<script>
    import services from '../services'
    import ContactItem from './contact/ContactItem';
    import Toolbar from './Toolbar'
    import HorizontalSlide from './transitions/HorizontalSlide'

    export default {
        name: 'recall',
        components: { ContactItem, Toolbar, HorizontalSlide },
        props: {
            page: {
                type: Number | String,
                required: false,
                default: 1
            }
        },
        mounted () {
            this.load()
        },
        data () {
            return {
                start: 0,
                end: 0,
                quantity: 0,
                current_page: 1,
                pages: 0,
                total: 0,
                loading: false,
                contacts: [],
                direction: 1
            }
        },
        methods: {
            goNext() {
                this.$router.push({params: {page: parseInt(this.page) + 1}})
            },
            goPrev() {
                this.$router.push({params: {page: parseInt(this.page) - 1}})
            },
            go (p) {
                this.$router.push({params: {page: parseInt(p)}})
            },
            refresh () {
                if (this.loading) return
                this.load()
            },
            load () {
                this.loading = true
                services.getContactRecall({page: this.page}).then(data => {
                    this.contacts = data.items
                    this.start = data.start + 1
                    this.end = data.end
                    this.current_page = data.page
                    this.pages = data.pages
                    this.total = data.total
                    this.quantity = data.qty
                    this.loading = false
                }).catch(error => {
                    //alert('error')
                    this.loading = false
                })
            }
        },
        watch: {
            page (new_value, prev_value) {
                const diff = prev_value - new_value
                this.direction = diff < 0 ? 1 : 0
                this.load()
            }
        },
        computed: {
            prevDisabled () {
                return !this.prev || this.loading
            },
            nextDisabled () {
                return !this.next || this.loading
            },
            prevPageLink () {
                if (!this.prev) return this.$route.path
                return this.basePath + '/' + this.prev
            },
            nextPageLink () {
                if (!this.next) return this.$route.path
                return this.basePath + '/' + this.next
            },
            basePath () {
                if (this.$route.params && this.$route.params.page) {
                    const index = this.$route.path.lastIndexOf('/')
                    return this.$route.path.substring(0, index)
                }
                return this.$route.path
            },
            next () {
                const next = parseInt(this.page) + 1
                if (next > this.pages) return undefined
                return next
            },
            prev () {
                const prev = parseInt(this.page) - 1
                if (prev === 0) return undefined
                return prev
            }
        }
    }
</script>
<style scoped>
    .left-0 {
        left: 0;
    }
    .right-0 {
        right: 0;
    }
</style>