<template>
    <div class="position-absolute p-5 d-flex flex-column position-0">        
        <fade-in-out>
            <div v-if="request" class="h-100 h-100 d-flex flex-column">
                
                <h4 class="font-weight-bolder mb-4">Ticket di {{ request.first_name }} {{ request.last_name }}</h4>
                
                <div class="bg-white rounded-lg mb-3 shadow-sm">
                    <div class="p-3 border-bottom">
                        <request-toolbar
                            :loading="loading"
                            :status="request.status"
                            :own="own"
                            :assigned="assigned"
                            :request_id="id"
                            :support="request.support"
                            @work="work"
                            @worked="worked"
                            @trash="trash"
                            @assign="assign"
                            @support="load"
                        />
                    </div>
                    <div class="p-3">
                        <div class="row pb-3 mb-3 border-bottom">
                            <div class="col">
                                <div class="d-block"><i class="fa fa-tag mr-2"></i>Tipologia</div> 
                                <div class="ml-4 font-weight-bold">{{ request.type_name }}</div>
                            </div>
                            <div class="col">
                                <div class="d-block"><i class="fas fa-star mr-2"></i>Categoria</div> 
                                <div class="ml-4 font-weight-bold">{{ request.category_name }}</div>
                            </div>
                            <div class="col">
                                <div class="d-block"><i class="fas fa-info-circle mr-2"></i>Oggetto</div> 
                                <div class="ml-4 font-weight-bold">{{ request.subject }}</div>
                            </div>
                            <div class="col">
                                <div class="d-block"><i class="fas fa-desktop mr-2"></i>Provenienza</div>
                                <div class="ml-4 font-weight-bold">{{ provider }}</div>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col">
                                <div class="d-block"><i class="far fa-envelope mr-2"></i>Stato</div> 
                                <div class="ml-4 font-weight-bold">{{ request.status_name }}</div>
                            </div>
                            <div class="col">
                                <div class="d-block"><i class="far fa-user-circle mr-2"></i>Operatore</div>
                                <div v-if="request.user_id" class="ml-4 font-weight-bold">{{ userName }}</div>
                            </div>
                            <div class="col">
                                <div class="d-block"><i class="far fa-calendar mr-2"></i>Ticket aperto</div> 
                                <div class="ml-4 font-weight-bold">
                                    <date-time :at="request.created_at" />
                                </div>
                            </div>
                            <div v-if="showPublicTicket" class="col no-print">
                                <div class="d-block"><i class="fas fa-external-link-alt mr-2"></i>Ticket pubblico</div>
                                <div class="ml-4 font-weight-bold"><router-link :to="`/ticket/${request.token}`">Vai alla pagina</router-link></div>
                            </div>
                            <div v-else-if="showTicketStatus" class="col">
                                <div class="d-block"><i class="far fa-calendar mr-2"></i>Ticket chiuso</div>
                                <div class="ml-4 font-weight-bold"><date-time :at="request.updated_at" /></div>
                            </div>
                            <div v-else class="col">
                            </div>
                        </div>
                    </div>
                </div>
                
                <div class="flex-grow-1">
                    <div class="position-relative h-100 w-100 overflow-x-hidden overflow-y-auto">
                        <div class="position-absolute w-100">

                            <ul class="list-group mb-3 shadow-sm">
                                <request-question
                                    :request="request"
                                />
                                <request-answer
                                    v-for="(reply, index) in request.replies"
                                    :id="reply.id"
                                    :support="{
                                        id: reply.support_id,
                                        done: reply.support_done,
                                        confirmed: reply.support_confirmed
                                    }"
                                    :own="own"
                                    :key="index"
                                    :cuid="reply.cuid"
                                    :name="`${reply.first_name} ${reply.last_name}`"
                                    :email="reply.email"
                                    :datetime="reply.created_at"
                                    :body="reply.body"
                                    :attachments="reply.attachments"
                                    :senderType="reply.sender_type"
                                    :token="request.token"
                                    :photo="reply.photo"
                                    @confirm="load"
                                    @ignore="load"
                                />
                            </ul>

                            <div v-if="waitingForOwnSupport" class="alert bg-warning p-4 rounded-0" role="alert">
                                <div class="d-flex align-items-center">
                                    <div class="mr-3"><i class="fas fa-exclamation-triangle fa-3x"></i></div>
                                    <div>
                                        <span class="h5 m-0 d-block">Hai richiesto l'intervento al reparto {{ request.support.group_name}} <date-time :at="request.support.created_at" /></span> 
                                        <span class="d-block">Attendi l'intervento prima di rispondere.</span>
                                    </div>
                                </div>
                            </div>

                            <div v-if="canReplyToSupport">
                                <div class="alert bg-warning p-4 rounded-0" role="alert">
                                    <div class="d-flex align-items-center">
                                        <div class="mr-3"><i class="fas fa-exclamation-triangle fa-3x"></i></div>
                                        <div class="flex-grow-1">
                                            <span class="h5 m-0 d-block">Rispondi alla richiesta di intervento di {{ request.support.user_first_name }} {{ request.support.user_last_name}} <date-time :at="request.support.created_at" /></span>
                                        </div>
                                    </div>
                                </div>
                                <div v-html="request.support.note"></div>
                                <request-input
                                    :support="request.support"
                                    :name="`${request.first_name} ${request.last_name}`"
                                    :email="request.email"
                                    :rid="request.id"
                                    :cuid="userId"
                                    :loading="loading.aswer"
                                    senderType="user"
                                    @sent="answered"
                                />
                            </div>

                            <div v-if="canWorkSupport">
                                <div class="alert bg-warning p-4 rounded-0" role="alert">
                                    <div class="d-flex align-items-center">
                                        <div class="mr-3"><i class="fas fa-exclamation-triangle fa-3x"></i></div>
                                        <div class="flex-grow-1">
                                            <span class="h5 m-0 d-block">{{ request.support.user_first_name }} {{ request.support.user_last_name}} ha richiesto un intervento a {{ request.support.group_name }} <date-time :at="request.support.created_at" /></span>
                                        </div>
                                        <div class="ml-3"><ui-button @click="assignSupport" color="secondary" icon="far fa-hand-point-right" outline>Prendi in Carico</ui-button></div>
                                    </div>
                                </div>
                                <div v-html="request.support.note"></div>
                            </div>

                            <request-input
                                v-if="answerable"
                                :name="`${request.first_name} ${request.last_name}`"
                                :email="request.email"
                                :rid="request.id"
                                :cuid="request.user_id"
                                :loading="loading.aswer"
                                senderType="user"
                                @sent="answered"
                            />

                        </div>
                    </div>
                </div>

            </div>
        </fade-in-out>

        <div v-if="!request" class="text-center p-5" style="font-size: 25px">
            <i class="fas fa-circle-notch fa-spin"></i>
        </div>

    </div>
</template>
<script>

    import { mapGetters } from 'vuex'
    import services from '../services'
    import LS from '../services/localstorage'
    import Page from './layout/Page'
    import RequestAnswer from './request/RequestAnswer'
    import RequestInput from './request/RequestInput'
    import RequestToolbar from './request/RequestToolbar'
    import RequestQuestion from './request/RequestQuestion'
    import Toolbar from './Toolbar'
    import FadeInOut from './transitions/FadeInOut'

    export default {
        name: 'request',
        components: {
            Page, Toolbar, FadeInOut,
            RequestToolbar, RequestAnswer, RequestInput, RequestQuestion
        },
        props: {
            id: {
                type: Number | String,
                required: true
            }
        },
        data () {
            return {
                request: false,
                answers: [],
                loading: {
                    assign: false,
                    trash: false,
                    worked: false,
                    towork: false
                }
            }
        },
        mounted () {
            this.load()
            services.ws.on('REQUEST_UPDATE', id =>  {
                if (parseInt(id) === parseInt(this.id)) {
                    this.load()
                }
            })
        },
        beforeDestroy () {
            services.ws.off('REQUEST_UPDATE')
        },
        methods: {
            load () {
                services.getRequest(this.id).then(data => {
                    this.request = data
                }).catch(error => {
                    alert(error)
                })
            },
            assignSupport () {
                this.loading.assign_support = true
                services.assignSupport(this.request.support.id).then(data => {
                    //TODO: alter support so that user can reply to it
                    this.request.support.touid = LS.get('user').id
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading.assign_support = false
                })
            },
            assign () {
                this.loading.assign = true
                services.assignRequest(this.id).then(data => {
                    if (data === true) {
                        this.request.status = 3
                        this.request.user_id = LS.get('user').id
                        this.request.user_first_name = LS.get('user').first_name
                        this.request.user_last_name = LS.get('user').last_name
                        this.request.status_name = 'In Lavorazione'
                    }
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading.assign = false
                })
            },
            work () {
                this.loading.towork = true
                services.setRequestAsToWork(this.id).then(data => {
                    if (data === true) {
                        this.request.status = 3
                        this.request.status_name = 'In Lavorazione'
                    }
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading.towork = false
                })
            },
            worked () {
                this.loading.worked = true
                services.setRequestAsWorked(this.id).then(data => {
                    if (data === true) {
                        this.request.status = 5
                        this.request.status_name = 'Lavorata'
                    }
                }).catch(error => {
                    alert(error)
                }).then(() => {
                    this.loading.worked = false
                })
            },
            trash () {
                this.loading.trash = true
                services.setRequestAsTrash(this.id).then(data => {
                    if (data === true) {
                        this.request.status = 6
                        this.request.status_name = 'Cestino'
                        this.loading.trash = false
                    }
                }).catch(error => {
                    this.loading.trash = false
                })
            },
            answered () {
                this.load()
            }
        },
        computed: {
            ...mapGetters('auth', {
                auth: 'user'
            }),
            user () {
                return LS.get('user')
            },
            userId () {
                return this.user.id
            },
            canReplyToSupport () {
                return !this.waitingForOwnSupport && this.request.support && this.request.support.touid === LS.get('user').id
            },
            canWorkSupport () {
                return !this.waitingForOwnSupport && this.request.support && this.request.support.togroup === LS.get('user').group && !this.request.support.touid
            },
            waitingForOwnSupport () {
                return this.request.support && this.request.support.uid === LS.get('user').id
            },
            answerable () {
                return (this.request.status === 2 || this.request.status === 3) && this.own
            },
            assigned () {
                return this.request.user_id !== 0
            },
            own () {
                return this.request.user_id === this.auth.id
            },
            userName () {
                return this.request.user_first_name + ' ' + this.request.user_last_name
            },
            provider () {
                switch (this.request.provider) {
                    case 'website':
                        return 'Sito Web'
                        break
                    case 'landing':
                        return 'Pubblicità - ' + this.request.campaign
                        break
                }
            },
            showPublicTicket () {
                return this.showTicketStatus && this.request.status !==  6 && this.request.status !== 5
            },
            showTicketStatus () {
                return this.request.type === 1 || this.request.type === 3
            }
        }
    }
</script>