
import io from 'socket.io-client';
import { EventEmitter } from 'events'

import { queryString } from './utils'
import LS from './localstorage'

class SocketService
{
    constructor (namespace, ignore_conn_ns = false)
    {
        //super()
        this.autoReconnectTimeout = 5 * 1000
        this.namespace = namespace || ''
        this.url = 'https://ws.unimercatorum.it'
        this.ignore_conn_ns = ignore_conn_ns
    }

    open ()
    {
        const user = LS.get('user')
        if (!user) return
        let connection = user.connection === 'local' ? '/mercatorum' : '/' + user.connection
        if (this.ignore_conn_ns) connection = ''
        this.socket = io(this.url + connection + this.namespace, {
            transports: ['websocket'],
            query: {
                token: user.token
            }
        })
        this.init()
    }


    off (type, fn = null) {
        if (fn) {

        } else {
            this.socket.removeAllListeners(type)
        }
        return this
    }
    
    on (type, fn)
    {
        if (this.socket) {
            this.socket.on(type, fn)
        }
        return this
    }

    emit (type, payload)
    {
        this.socket.emit(type, payload)
        return this
    }

    init ()
    {
        this.socket.on('connect', () => this.emit('connection'))
        this.socket.on('connect_error', error => this.emit('connection_error', error))
        this.socket.on('connect_timeout', this.emit('connection_timeout'));
    }

    send(type, payload = null)
    {
        this.socket.emit(type, payload)
    }
}

export default SocketService